<template>
  <div id="home">
		<vui-row>
			<div class="col-sm-6">
				<h1 style="color:#42b883">Vui</h1>
				<p class="lead">
					Is a Vue 2.0 component library for designers and front-end developers
					based on Bootstrap 4. Vui is an open source toolkit for developing
					single page applications.
				</p>
				<p class="lead">
					Quickly prototype your ideas or build your entire app with an extensive
					collection of prebuilt components that are easy to use and very easy to customize to 
					fit your exact needs.
				</p>
				<div class="d-flex">
					<button class="btn btn-vui btn-lg w-50 mr-3">Get Started</button>
					<vui-button class="w-50" color="secondary" size="lg" outline>Download</vui-button>
				</div>
			</div>

			<div class="col-sm-6">
				<img src="../../static/images/vui-logo.svg" id="vui-logo" />
			</div>
		</vui-row>
		<div class="footer">
			<vui-nav class="justify-content-center">
				<vui-nav-item to="/">Home</vui-nav-item>
				<vui-nav-item to="/docs/getting-started/intro">Documentation</vui-nav-item>
				<vui-nav-item to="/docs/download">Download</vui-nav-item>
				<vui-nav-item to="/patrons">Patron Supporters</vui-nav-item>
			</vui-nav>
			<div class="footer-text">
				Designed and built with love and passion for Vue.js and web components by 
				<a href="https://twitter.com/joe_lomoglio" target="_blank">@joe_lomoglio</a>. 
				Code licensed <a href="#" target="_blank">MIT.</a>
			</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to VUI'
    }
	},
	mounted() {
		$(document).find('#docLink').removeClass('active')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
	overflow-x: hidden !important;
}

#home {
	padding-top: 100px;
}

h1 {
	font-weight: bold;
	color: darkgreen;
	font-size: 6em;
}

.btn-vui {
	background-color: white;
	color: #42b883;
	border: 1px solid #42b883;
}
.btn-vui:hover {
	background-color: #42b883;
	color: white;
}

#vui-logo {
	width: 70%;
}

.footer {
	background-color: #eee;
	height: 120px;
	position: absolute;
	left: 0px;
	right: 0px;
	bottom: 0;
	padding: 20px;
	text-align: center;
}

.footer-text {
	font-size: .8em;
}
</style>



