import Vue from 'vue'
import App from './App'
import router from './router/router'
import Code from './components/Code'
import './vui/vui.js'

Vue.config.productionTip = false

Vue.component('vuiCode', Code)

new Vue({
  el: '#app',
	router,
  components: { 
		App
	},
  template: '<App/>'
})



