<template>
	<div class="container">
		<h1 class="display-4">Code Panel</h1>
		<p class="lead">
			Documentation and examples for how to use Vuis included code panel component.
		</p>

		<br>

		<h3 id="overview">Overview</h3>
		<p>
			The Code Panel is the component that has been used throughout the documentation to 
			display live examples and code blocks. When I started creating the documentation, I searched
			the web high and low to find such a component and I couldn't find anything, so I decided that
			I would build one from scatch and now I want to share it with everyone.
		</p>

		<br>

		<p>
			Below is the component and it's displaying it's code. It has two <code>slots</code> one for the example
			code that will be rendered as the live component, and the second one for the <code>code</code>. You place
			the code that renders as a live example in the <code>example</code> slot. For the <code>code</code> slot,
			you need to create a leterial string that contains all the code you want to display as code:
		</p>
		<code>const example1 = ``</code>
		<br><br>
		<p>
			If you place the code directly in the <code>code</code> slot it will not display properly. It's also not 
			possible to display the literal string in the <code>code</code> slot, as you can't have a lteral string within
			a literal string.
		</p>
		<p>
			If you leave the <code>example</code> slot blank it will not be displayed and only the <code>code</code>
			section will display as with the example below.
		</p>
		<vui-code-panel>
			<template slot="example">
				
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		<br>
		<p>
			The code panel has a single prop for setting the <code>type</code>. There are two supported types:
		</p>
		<ul>
			<li><code>html (default)</code></li>
			<li><code>javascript</code></li>
		</ul>
		<vui-code-panel>
			<template slot="example">
				
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-code-panel>
	<template slot="example">
		<!-- Actual component to be rendered goes here -->
	</template>
	<template slot="code">{{ example1 }}</template>
</vui-code-panel>
`
const ex2 = `
<vui-code-panel type="html">
	<template slot="example">
		<!-- Actual component to be rendered goes here -->
	</template>
	<template slot="code">{{ example2 }}</template>
</vui-code-panel>
`

export default {
	name: 'Docs-',
	data() {
		return {
			example1: ex1,
			example2: ex2
		}
	},
	mounted() {
		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


