<template>
	<div class="container">
		<h1 class="display-4">Card</h1>
		<p class="lead">
			Vuis cards provide a flexible and extensible content 
			container with multiple variants and options.
		</p>

		<h3 id="about">About</h3>
		<p>
			A card is a flexible and extensible content container. 
			It includes options for headers and footers, a wide variety 
			of content, contextual background colors, and powerful display 
			options.
		</p>

		<h3 id="example">Example</h3>
		<p>
			Cards are built with as little markup and styles as possible, 
			but still manage to deliver a ton of control and customization. 
			Built with flexbox, they offer easy alignment and mix well with 
			other Vui components.
		</p>
		<p>
			Below is an example of a basic card with mixed content and a 
			fixed width. Cards have no fixed width to start, so theyll 
			naturally fill the full width of its parent element.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-card width="18rem">
					<vui-img-placeholder size="286x180" color="secondary" />
					<vui-card-body>
						<vui-card-title>Card title</vui-card-title>
						<vui-card-text>
							Some quick example text to build on the card title 
							and make up the bulk of the card's content.
						</vui-card-text>
						<vui-button color="primary">Go somewhere</vui-button>
					</vui-card-body>
				</vui-card>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">width</th>
					<td>Width of the card.</td>
					<td>px, rem, %</td>
				</tr>
			</tbody>
		</table>
		
		<br><br>

		<h3 id="content_types">Content Types</h3>
		<p>
			Cards support a wide variety of content, including images, 
			text, list groups, links, and more. Below are examples of whats 
			supported.
		</p>

		<br>

		<h3 id="body">Body</h3>
		<p>
			The building block of a card is the <code>&lt;vui-card-body&gt;</code>
			Use it whenever you need a padded section within a card.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-card width="18rem">
					<vui-card-body>
						This is some text within a card body.
					</vui-card-body>
				</vui-card>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="title">Title, Subtitle & Text</h3>
		<p>
			Card titles are used by adding <code>&lt;vui-card-title&gt;</code>
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-card width="18rem">
					<vui-card-body>
						<vui-card-title>Card title</vui-card-title>
						<vui-card-subtitle>Card subtitle</vui-card-subtitle>
						<vui-card-text>
							Some quick example text to build on the card title 
							and make up the bulk of the card's content.
						</vui-card-text>
					</vui-card-body>
				</vui-card>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="links">Links</h3>
		<p>
			Card links are used by adding <code>&lt;vui-card-link&gt;</code>. A
			card-link has two props <code>href</code> & <code>target</code>
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-card width="18rem">
					<vui-card-body>
						<vui-card-title>Card title</vui-card-title>
						<vui-card-subtitle>Card subtitle</vui-card-subtitle>
						<vui-card-text>
							Some quick example text to build on the card title 
							and make up the bulk of the card's content.
						</vui-card-text>
						<vui-card-link href="#" target="_blank">Card Link</vui-card-link>
						<vui-card-link href="#" target="_blank">Another Link</vui-card-link>
					</vui-card-body>
				</vui-card>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="images">Images</h3>
		<p>
			Using the <code>&lt;vui-card-image&gt;</code> lets you easily add and
			position a caption over the image.
			Alternativley you can also use the <code>&lt;vui-img-placeholder&gt;</code>
			component if you are prototyping and need a quick placeholder and do not
			have an image to use. 
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-row>
					<div class="col-sm-6">
						<vui-card>
							<vui-card-image
								src="../../static/images/blank.svg" 
								cap="Image Caption"
								capAlign="middle" 
							/>
							<vui-card-body>
								Some quick example text to build on the card title 
								and make up the bulk of the card's content.
							</vui-card-body>
						</vui-card>
					</div>
					<div class="col-sm-6">
						<vui-card>
							<vui-card-body>
								Some quick example text to build on the card title 
								and make up the bulk of the card's content.
							</vui-card-body>
							<vui-card-image
								src="../../static/images/blank.svg" 
								cap="Image Caption"
								capAlign="middle" 
							/>
						</vui-card>
					</div>
				</vui-row>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">src</th>
					<td>The path to the image.</td>
					<td>n/a</td>
				</tr>
				<tr>
					<th scope="row">cap</th>
					<td>A caption for the image.</td>
					<td>Text only</td>
				</tr>
				<tr>
					<th scope="row">capAlign</th>
					<td>Vertical positions the caption.</td>
					<td>Top, Middle, Bottom</td>
				</tr>
				<tr>
					<th scope="row">capSize</th>
					<td>Sets the font-size of the caption text.</td>
					<td>h1, h2, h3, h4, h5, h6</td>
				</tr>
				<tr>
					<th scope="row">capColor</th>
					<td>Sets the color of the caption text.</td>
					<td>light, secondary, dark</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="header_footer">Header and Footer</h3>
		<p>
			Add an optional header and/or footer within a card.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-card width="18rem">
					<vui-card-header color="primary" size="h5">Card Header</vui-card-header>
					<vui-card-body>
						<vui-card-title>Card title</vui-card-title>
						<vui-card-text>
							Some quick example text to build on the card title 
							and make up the bulk of the card's content.
						</vui-card-text>
					</vui-card-body>
					<vui-card-footer>Card Footer</vui-card-footer>
				</vui-card>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">color</th>
					<td>Sets the background color of the header or footer.</td>
					<td>primary, secondary, success, danger, warning, info, light, dark</td>
				</tr>
				<tr>
					<th scope="row">size</th>
					<td>Sets the size of the header or footer text.</td>
					<td>h1, h2, h3, h4, h5, h6</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="text_alignment">Text Alignment</h3>
		<p>
			You can quickly change the text alignment of any cardin its entirety
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-card width="18rem" align="center">
					<vui-card-header color="primary" size="h5">Card Header</vui-card-header>
					<vui-card-body>
						<vui-card-title>Card title</vui-card-title>
						<vui-card-text>
							Some quick example text to build on the card title 
							and make up the bulk of the card's content.
						</vui-card-text>
					</vui-card-body>
				</vui-card>
			</template>
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">align</th>
					<td>Sets the text alignment of the entire card.</td>
					<td>left, center, right</td>
				</tr>
			</tbody>
		</table>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-card width="18rem">
	<vui-img-placeholder size="286x180" color="secondary" />
	<vui-card-body>
		<vui-card-title>Card title</vui-card-title>
		<vui-card-text>
			Some quick example text to build on the card title 
			and make up the bulk of the card's content.
		</vui-card-text>
		<vui-button color="primary">Go somewhere</vui-button>
	</vui-card-body>
</vui-card>
`

const ex2 = `
<vui-card width="18rem">
	<vui-card-body>
		This is some text within a card body.
	</vui-card-body>
</vui-card>
`

const ex3 = `
<vui-card>
	<vui-card-body>
		<vui-card-title>Card title</vui-card-title>
		<vui-card-subtitle>Card Subtitle</vui-card-subtitle>
		<vui-card-text>
			Some quick example text to build on the card title 
			and make up the bulk of the card's content.
		</vui-card-text>
	</vui-card-body>
</vui-card>
`

const ex4 = `
<vui-card width="18rem">
	<vui-card-body>
		<vui-card-title>Card title</vui-card-title>
		<vui-card-subtitle>Card subtitle</vui-card-subtitle>
		<vui-card-text>
			Some quick example text to build on the card title 
			and make up the bulk of the card's content.
		</vui-card-text>
		<vui-card-link href="#" target="_blank">Card Link</vui-card-link>
		<vui-card-link href="#" target="_blank">Another Link</vui-card-link>
	</vui-card-body>
</vui-card>
`

const ex5 = `
<vui-card width="18rem">
	<vui-card-image src="..." cap="Image Caption" capAlign="middle" />
	<vui-card-body>
		Some quick example text to build on the card title 
		and make up the bulk of the card's content.
	</vui-card-body>
</vui-card>

<vui-card width="18rem">
	<vui-card-body>
		Some quick example text to build on the card title 
		and make up the bulk of the card's content.
	</vui-card-body>
	<vui-card-image src="..." cap="Image Caption" capAlign="middle" />
</vui-card>
`

const ex6 = `
<vui-card width="18rem">
	<vui-card-header color="primary" size="h5">Card Header</vui-card-header>
	<vui-card-body>
		<vui-card-title>Card title</vui-card-title>
		<vui-card-text>
			Some quick example text to build on the card title 
			and make up the bulk of the card's content.
		</vui-card-text>
	</vui-card-body>
	<vui-card-footer>Card Footer</vui-card-footer>
</vui-card>
`

const ex7 = `
<vui-card width="18rem" align="center">
	<vui-card-header color="primary" size="h5">Card Header</vui-card-header>
	<vui-card-body>
		<vui-card-title>Card title</vui-card-title>
		<vui-card-text>
			Some quick example text to build on the card title 
			and make up the bulk of the card's content.
		</vui-card-text>
	</vui-card-body>
</vui-card>
`

export default {
	name: 'Docs-Card',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Example', link: 'example' },
			{ label: 'Content Types', link: 'content_types' },
			{ label: '- Body', link: 'body' },
			{ label: '- Title', link: 'title' },
			{ label: '- Links', link: 'links' },
			{ label: '- Images', link: 'images' },
			{ label: 'Header & Footer', link: 'header_footer' },
			{ label: 'Text Alignment', link: 'text_alignment' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500)
		}
	}
}
</script>


