<template>
	<div class="container">
		<h1 class="display-4">Callout</h1>
		<p class="lead">
			Documentation and examples for how to use Vuis callout component.
		</p>

		<br>

		<h3 id="about">About</h3>
		<p>
			A <strong>callout</strong> is an informative content container. It's intended
			to be used to draw attention to information you want to convey to a user. Callout's
			have four types:
		</p>
		<ul>
			<li>Info</li>
			<li>Danger</li>
			<li>Warning</li>
			<li>Success</li>
		</ul>

		<br>

		<h3 id="examples">Examples</h3>
		<p>
			<strong>Callout's</strong> are built with as little markup and styles as possible.
			Built with flexbox, they offer easy alignment and mix well 
			with other Vui components. By default they have a top and bottom margin.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav>
					<vui-callout>
						Info: (default) Used this to inform a user about something that is general and of little priority.
					</vui-callout>
					<vui-callout type="danger">
						Danger: Used this to inform a user about something critical.
					</vui-callout>
					<vui-callout type="warning">
						Warning: Used this to inform a user about something that they should be concerend with.
					</vui-callout>
					<vui-callout type="success">
						Success: Used this to inform a user about a positive or successful change or update.
					</vui-callout>
				</vui-nav>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-callout>
	Used this to inform a user ... little priority.
</vui-callout>

<vui-callout type="danger">
	Used this to inform ... critical.
</vui-callout>

<vui-callout type="warning">
	Used this to inform ... concerend with.
</vui-callout>

<vui-callout type="success">
	Used this to inform ... change or update.
</vui-callout>
`

export default {
	name: 'Docs-',
	data() {
		return {
			example1: ex1
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'About', link: 'about' },
			{ label: 'Examples', link: 'examples' }
			
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


