<template>
	<div class="container">
		<h1 class="display-4">Input Group</h1>
		<p class="lead">
			Easily extend form controls by adding text, buttons, or button groups on either 
			side of textual inputs, custom selects, and custom file inputs.
		</p>

		<h3 id="example">Basic Example</h3>
		<p>
			Place one add-on or button on either side of an input. You may also place one on 
			both sides of an input. Remember to place <code>&lt;vui-label&gt;</code>s outside 
			the <code>&lt;vui-input-group&gt;</code>.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3">
					<vui-input-group-prepend>@</vui-input-group-prepend>
					<vui-input type="text" placeholder="Username" />
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input type="text" placeholder="Recipient's username" />
					<vui-input-group-append text>@example.com</vui-input-group-append>
				</vui-input-group>

				<vui-form-label>Your vanity URL</vui-form-label>
				<vui-input-group class="mb-3">
					<vui-input-group-prepend text>https://example.com/users/</vui-input-group-prepend>
					<vui-input type="text" />
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input-group-prepend text>$</vui-input-group-prepend>
					<vui-input type="text" />
					<vui-input-group-append text>.00</vui-input-group-append>
				</vui-input-group>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="sizing">Sizing</h3>
		<p>
			Add the relative form sizing classes to the <code>&lt;vui-input-group&gt;</code> itself and contents 
			within will automatically resizeno need for repeating the <code>size</code> prop on each element.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3" size="sm">
					<vui-input-group-prepend>Small</vui-input-group-prepend>
					<vui-input type="text" placeholder="Username" />
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input-group-prepend>Default</vui-input-group-prepend>
					<vui-input type="text" placeholder="Username" />
				</vui-input-group>

				<vui-input-group class="mb-3" size="lg">
					<vui-input-group-prepend>Large</vui-input-group-prepend>
					<vui-input type="text" placeholder="Username" />
				</vui-input-group>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">size</th>
					<td>Provides relative sizing.</td>
					<td>sm, lg</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="checkboxes">Checkboxes and Radios</h3>
		<p>
			Add the relative form sizing classes to the <code>&lt;vui-input-group&gt;</code> itself and contents 
			within will automatically resizeno need for repeating the <code>size</code> prop on each element.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3" size="sm">
					<vui-input-group-prepend>
						<vui-checkbox id="chkbox1" color="primary" />
					</vui-input-group-prepend>
					<vui-input type="text" />
				</vui-input-group>

				<vui-input-group class="mb-3" size="sm">
					<vui-input-group-prepend>
						<vui-radio color="primary" />
					</vui-input-group-prepend>
					<vui-input type="text" />
				</vui-input-group>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="multiple_inputs">Multiple Inputs</h3>
		<p>
			While multiple <code>&lt;vui-input&gt;</code>s are supported visually, validation styles are 
			only available for input groups with a single <code>&lt;vui-input&gt;</code>
		</p>	
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3" size="sm">
					<vui-input-group-prepend>
						First and Last Name
					</vui-input-group-prepend>
					<vui-input type="text" />
					<vui-input type="text" />
				</vui-input-group>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="button_addons">Button Addons</h3>
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3">
					<vui-input-group-prepend button>
						<vui-button outline>Button</vui-button>
					</vui-input-group-prepend>
					<vui-input type="text" />
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input type="text" />
					<vui-input-group-append button>
						<vui-button outline>Button</vui-button>
					</vui-input-group-append>
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input-group-prepend button>
						<vui-button outline>Button</vui-button>
						<vui-button outline>Button</vui-button>
					</vui-input-group-prepend>
					<vui-input type="text" />
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input type="text" />
					<vui-input-group-append button>
						<vui-button outline>Button</vui-button>
						<vui-button outline>Button</vui-button>
					</vui-input-group-append>
				</vui-input-group>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">button</th>
					<td>Required when using any type of button, removes all padding.</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="dropdown_buttons">Dropdown Buttons</h3>
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3">
					<vui-input-group-prepend button>
						<vui-dropdown-button text="Dropdown" outline>
							<vui-dropdown-item href="#">Call Function</vui-dropdown-item>
							<vui-dropdown-item href="#">Goto Route</vui-dropdown-item>
							<vui-dropdown-item href="#">Download File</vui-dropdown-item>
						</vui-dropdown-button>
					</vui-input-group-prepend>
					<vui-input type="text" />
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input type="text" />
					<vui-input-group-append button>
						<vui-dropdown-button text="Dropdown" outline>
							<vui-dropdown-item href="#">Call Function</vui-dropdown-item>
							<vui-dropdown-item href="#">Goto Route</vui-dropdown-item>
							<vui-dropdown-item href="#">Download File</vui-dropdown-item>
						</vui-dropdown-button>
					</vui-input-group-append>
				</vui-input-group>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="split_buttons">Split Buttons</h3>
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3">
					<vui-input-group-prepend button>
						<vui-dropdown-button-split text="Action" outline>
							<vui-dropdown-item href="#">Call Function</vui-dropdown-item>
							<vui-dropdown-item href="#">Goto Route</vui-dropdown-item>
							<vui-dropdown-item href="#">Download File</vui-dropdown-item>
						</vui-dropdown-button-split>
					</vui-input-group-prepend>
					<vui-input type="text" />
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input type="text" />
					<vui-input-group-append button>
						<vui-dropdown-button-split text="Dropdown" outline>
							<vui-dropdown-item href="#">Call Function</vui-dropdown-item>
							<vui-dropdown-item href="#">Goto Route</vui-dropdown-item>
							<vui-dropdown-item href="#">Download File</vui-dropdown-item>
						</vui-dropdown-button-split>
					</vui-input-group-append>
				</vui-input-group>
			</template>
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="custom_select">Custom Select</h3>
		<p>
			Text can only be applied to the <code>vui-input-group-prepend</code> and buttons
			can be applied to both.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-input-group class="mb-3">
					<vui-input-group-prepend>Options</vui-input-group-prepend>
					<vui-select id="select1">
						<vui-option>Select an option...</vui-option>
						<vui-option value="1">Option 1</vui-option>
						<vui-option value="2">Option 2</vui-option>
						<vui-option value="3">Option 3</vui-option>
						<vui-option value="4">Option 4</vui-option>
						<vui-option value="5">Option 5</vui-option>
					</vui-select>
				</vui-input-group>

				<vui-input-group class="mb-3">
					<vui-input-group-prepend button>
						<vui-button outline>Button</vui-button>
					</vui-input-group-prepend>
					<vui-select id="select1">
						<vui-option>Select an option...</vui-option>
						<vui-option value="1">Option 1</vui-option>
						<vui-option value="2">Option 2</vui-option>
						<vui-option value="3">Option 3</vui-option>
						<vui-option value="4">Option 4</vui-option>
						<vui-option value="5">Option 5</vui-option>
					</vui-select>
				</vui-input-group>
			
				<vui-input-group class="mb-3">
					<vui-select id="select1">
						<vui-option>Select an option...</vui-option>
						<vui-option>Option 1</vui-option>
						<vui-option>Option 2</vui-option>
						<vui-option>Option 3</vui-option>
						<vui-option>Option 4</vui-option>
						<vui-option>Option 5</vui-option>
					</vui-select>
					<vui-input-group-append button>
						<vui-button outline>Button</vui-button>
					</vui-input-group-append>
				</vui-input-group>
			</template>
			<template slot="code">{{ example8 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-input-group class="mb-3">
	<vui-input-group-prepend>@</vui-input-group-prepend>
	<vui-input type="text" placeholder="Username" />
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input type="text" placeholder="Recipient's username" />
	<vui-input-group-append>@example.com</vui-input-group-append>
</vui-input-group>

<vui-form-label>Your vanity URL</vui-form-label>
<vui-input-group class="mb-3">
	<vui-input-group-prepend>https://example.com/users/</vui-input-group-prepend>
	<vui-input type="text" />
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input-group-prepend>$</vui-input-group-prepend>
	<vui-input type="text" />
	<vui-input-group-append>.00</vui-input-group-append>
</vui-input-group>
`
const ex2 = `
<vui-input-group class="mb-3" size="sm">
	<vui-input-group-prepend>Small</vui-input-group-prepend>
	<vui-input type="text" placeholder="Username" />
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input-group-prepend>Default</vui-input-group-prepend>
	<vui-input type="text" placeholder="Username" />
</vui-input-group>

<vui-input-group class="mb-3" size="lg">
	<vui-input-group-prepend>Large</vui-input-group-prepend>
	<vui-input type="text" placeholder="Username" />
</vui-input-group>
`
const ex3 = `
<vui-input-group class="mb-3" size="sm">
	<vui-input-group-prepend>
		<vui-checkbox id="chkbox1" color="primary" />
	</vui-input-group-prepend>
	<vui-input type="text" />
</vui-input-group>

<vui-input-group class="mb-3" size="sm">
	<vui-input-group-prepend>
		<vui-radio id="radio1" color="primary" />
	</vui-input-group-prepend>
	<vui-input type="text" />
</vui-input-group>
`
const ex4 = `
<vui-input-group class="mb-3" size="sm">
	<vui-input-group-prepend>
		First and Last Name
	</vui-input-group-prepend>
	<vui-input type="text" />
	<vui-input type="text" />
</vui-input-group>
`
const ex5 = `
<vui-input-group class="mb-3">
	<vui-input-group-prepend button>
		<vui-button outline>Button</vui-button>
	</vui-input-group-prepend>
	<vui-input type="text" />
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input type="text" />
	<vui-input-group-append button>
		<vui-button outline>Button</vui-button>
	</vui-input-group-append>
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input-group-prepend button>
		<vui-button outline>Button</vui-button>
		<vui-button outline>Button</vui-button>
	</vui-input-group-prepend>
	<vui-input type="text" />
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input type="text" />
	<vui-input-group-append button>
		<vui-button outline>Button</vui-button>
		<vui-button outline>Button</vui-button>
	</vui-input-group-append>
</vui-input-group>
`
const ex6 = `
<vui-input-group class="mb-3">
	<vui-input-group-prepend button>
		<vui-dropdown-button text="Dropdown" outline>
			<vui-dropdown-item @click="anotherFnc">Call Function</vui-dropdown-item>
			<vui-dropdown-item to="...">Goto Route</vui-dropdown-item>
			<vui-dropdown-item href="...">Download File</vui-dropdown-item>
		</vui-dropdown-button>
	</vui-input-group-prepend>
	<vui-input type="text" />
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input type="text" />
	<vui-input-group-append button>
		<vui-dropdown-button text="Dropdown" outline>
			<vui-dropdown-item @click="anotherFnc">Call Function</vui-dropdown-item>
			<vui-dropdown-item to="...">Goto Route</vui-dropdown-item>
			<vui-dropdown-item href="...">Download File</vui-dropdown-item>
		</vui-dropdown-button>
	</vui-input-group-append>
</vui-input-group>
`
const ex7 = `
<vui-input-group class="mb-3">
	<vui-input-group-prepend button>
		<vui-dropdown-button-split text="Action" outline>
			<vui-dropdown-item>Call Function</vui-dropdown-item>
			<vui-dropdown-item>Goto Route</vui-dropdown-item>
			<vui-dropdown-item>Download File</vui-dropdown-item>
		</vui-dropdown-button-split>
	</vui-input-group-prepend>
	<vui-input type="text" />
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input type="text" />
	<vui-input-group-append button>
		<vui-dropdown-button-split text="Dropdown" outline>
			<vui-dropdown-item>Call Function</vui-dropdown-item>
			<vui-dropdown-item>Goto Route</vui-dropdown-item>
			<vui-dropdown-item>Download File</vui-dropdown-item>
		</vui-dropdown-button-split>
	</vui-input-group-append>
</vui-input-group>
`
const ex8 = `
<vui-input-group class="mb-3">
	<vui-input-group-prepend>Options</vui-input-group-prepend>
	<vui-select id="select1">
		<vui-option>Select an option...</vui-option>
		<vui-option value="1">Option 1</vui-option>
		<vui-option value="2">Option 2</vui-option>
		<vui-option value="3">Option 3</vui-option>
		<vui-option value="4">Option 4</vui-option>
		<vui-option value="5">Option 5</vui-option>
	</vui-select>
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-input-group-prepend button>
		<vui-button outline>Button</vui-button>
	</vui-input-group-prepend>
	<vui-select id="select1">
		<vui-option>Select an option...</vui-option>
		<vui-option value="1">Option 1</vui-option>
		<vui-option value="2">Option 2</vui-option>
		<vui-option value="3">Option 3</vui-option>
		<vui-option value="4">Option 4</vui-option>
		<vui-option value="5">Option 5</vui-option>
	</vui-select>
</vui-input-group>

<vui-input-group class="mb-3">
	<vui-select id="select1">
		<vui-option>Select an option...</vui-option>
		<vui-option>Option 1</vui-option>
		<vui-option>Option 2</vui-option>
		<vui-option>Option 3</vui-option>
		<vui-option>Option 4</vui-option>
		<vui-option>Option 5</vui-option>
	</vui-select>
	<vui-input-group-append button>
		<vui-button outline>Button</vui-button>
	</vui-input-group-append>
</vui-input-group>
`

export default {
	name: 'Docs-InputGroup',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Basic Example', link: 'example' },
			{ label: 'Sizing', link: 'sizing' },
			{ label: 'Checkboxes & Radios', link: 'checkboxes' },
			{ label: 'Multiple Inputs', link: 'multiple_inputs' },
			{ label: 'Button Addons', link: 'button_addons' },
			{ label: 'Dropdown Buttons', link: 'dropdown_buttons' },
			{ label: 'Split Buttons', link: 'split_buttons' },
			{ label: 'Custom Select', link: 'custom_select' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500)
		}
	}
}
</script>


