<template>
	<div class="container">
		<h1 class="display-4">Draggable</h1>
		<p class="lead">
			Use the drag utility directive to make any element or component draggable.
		</p>

		<br>

		<h3 id="overview">Overview</h3>
		<p>
			Provides a surprisingly simple way to make virtually any DOM element draggable.
		</p>
		<ul>
			<li>Touch enabled - works great on tablets, phones, and desktop browsers.</li>
			<li>Defineable drag boundrary regions for containing the draggable element.</li>
			<li>Definable drag handle element for triggering the dragging.</li>
			<li>Perfect for creating draggable windows or dialouges.</li>
		</ul>

		<br><br>

		<h3 id="example">Example</h3>
		<p>
			To make an element or component draggable you first need to setup the following:
		</p>
		<ul>
			<li>
				<code>Drag Container</code>: Any element that you want to limit the draggable element to.
				You will need to reference this elements id. The id can be any name, its just a reference.
			</li>
			<li>
				<code>Drag Handle</code>: The element you want to trigger the dragging. In the example below
				the card header is assigned as the drag handle. This means you can only drag the card by grabbing
				anywhere within the card header. You can reference a class name or id. Id's are reccomended if you 
				plan on having multiple draggable elements.
			</li>
		</ul>
		<p>
			Let's take a closer look at the <code>v-drag:drag-container="'.drag-handle'"</code> utility directive.
			It works by binding the id of the element you want to set as the drag container. This is required. 
			In our example the id is <code>drag-container</code> without the <code>#</code> symbol. 
		</p>
		<p>	
			The next part is the class name or id of the element you want to assign as the optional drag handle: 
			<code>v-drag:drag-container="'.drag-handle'"</code>, in our 
			example the class name <code>'.drag-handle'</code> is passed in, you must include the <code>.</code> for classes,
			and a <code>#</code> for id's. You must also wrap theses in single quotes when paassing them in directly like
			in the example. If you pass in a data variable or computed property, you can ommit the single quotes.
		</p>
		<vui-code-panel>
			<template slot="example">
				<div id="drag-container">
					<vui-card width="18rem" v-shadow v-drag:drag-container="'.drag-handle'">
						<vui-card-header class="drag-handle" color="primary" size="h5">Card Header</vui-card-header>
						<vui-card-body>
							<vui-card-title>Card title</vui-card-title>
							<vui-card-text>
								Grab the card header to drag the card around.
							</vui-card-text>
						</vui-card-body>
					</vui-card>
				</div>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<div id="drag-container">
	<vui-card width="18rem" v-shadow v-drag:drag-container="'.drag-handle'">
		<vui-card-header class="drag-handle" color="primary" size="h5">Card Header</vui-card-header>
		<vui-card-body>
			...
		</vui-card-body>
	</vui-card>
</div>
`
const ex2 = `

`
const ex3 = `

`
const ex4 = `

`
const ex5 = `

`
const ex6 = `

`
const ex7 = `

`
const ex8 = `

`
const ex9 = `

`

export default {
	name: 'Docs-Draggable',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8,
			example9: ex9
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Overview', link: 'overview' },
			{ label: 'Example', link: 'example' },
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


