<template>
	<div class="container">
		<h1 class="display-4">Vertical Align</h1>
		<p class="lead">
			Easily change the vertical alignment of inline, inline-block, inline-table, and table cell elements.
		</p>

		<br>

		<p>
			Change the alignment of elements with the <code>v-align</code> utility directive. Please note that vertical-align only affects 
			inline, inline-block, inline-table, and table cell elements.
		</p>
		<p>
			There are six vertical alignment types:
		</p>
		<ul>
			<li><code>baseline</code></li>
			<li><code>top</code></li>
			<li><code>middle</code></li>
			<li><code>bottom</code></li>
			<li><code>text-top</code></li>
			<li><code>text-bottom</code></li>
		</ul>
		<p>With inline elements:</p>
		<vui-code-panel>
			<template slot="example">
				<span v-align:baseline>baseline</span>
				<span v-align:top>top</span>
				<span v-align:middle>middle</span>
				<span v-align:bottom>bottom</span>
				<span v-align:text-top>text-top</span>
				<span v-align:text-bottom>text-bottom</span>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br>
		<p>
			With table cells:
		</p>
		<vui-code-panel>
			<template slot="example">
				<table style="height: 100px;">
					<tbody>
						<tr>
							<td v-align:baseline>baseline</td>
							<td v-align:top>top</td>
							<td v-align:middle>middle</td>
							<td v-align:bottom>bottom</td>
							<td v-align:text-top>text-top</td>
							<td v-align:text-bottom>text-bottom</td>
						</tr>
					</tbody>
				</table>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<span v-align:baseline>baseline</span>
<span v-align:top>top</span>
<span v-align:middle>middle</span>
<span v-align:bottom>bottom</span>
<span v-align:text-top>text-top</span>
<span v-align:text-bottom>text-bottom</span>
`
const ex2 = `
<table style="height: 100px;">
	<tbody>
		<tr>
			<td v-align:baseline>baseline</td>
			<td v-align:top>top</td>
			<td v-align:middle>middle</td>
			<td v-align:bottom>bottom</td>
			<td v-align:text-top>text-top</td>
			<td v-align:text-bottom>text-bottom</td>
		</tr>
	</tbody>
</table>
`

export default {
	name: 'Docs-VerticalAlign',
	data() {
		return {
			example1: ex1,
			example2: ex2
		}
	},
	mounted() {
		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


