<template>
	<div class="container">
		<h1 class="display-4">Buttons</h1>
		<p class="lead">
			Use Vuis custom buttons for actions in forms, dialogs, 
			and more with support for multiple sizes, states, and more.
		</p>

		<h3 id="examples">Examples</h3>
		<p>
			Using Bootstrap's predefined colors, you can customize each button 
			giving each its own semantic purpose. By default the button uses
			the <code>secondary</code> color.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary">Primary</vui-button>
				<vui-button color="secondary">Secondary</vui-button>
				<vui-button color="success">Success</vui-button>
				<vui-button color="danger">Danger</vui-button>
				<vui-button color="warning">Warning</vui-button>
				<vui-button color="info">Info</vui-button>
				<vui-button color="light">Light</vui-button>
				<vui-button color="dark">Dark</vui-button>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">color</th>
					<td>Color of the button.</td>
					<td>primary, secondary, danger, warning, info, light, dark</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="outline_button">Outline Buttons</h3>
		<p>
			In need of a button, but not the hefty background colors they bring? 
			Change the default appearnce with the <code>outline</code> prop
			to remove all background images and colors on any button.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary" outline>Primary</vui-button>
				<vui-button color="secondary" outline>Secondary</vui-button>
				<vui-button color="success" outline>Success</vui-button>
				<vui-button color="danger" outline>Danger</vui-button>
				<vui-button color="warning" outline>Warning</vui-button>
				<vui-button color="info" outline>Info</vui-button>
				<vui-button color="dark" outline>Dark</vui-button>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">color</th>
					<td>Color of the button.</td>
					<td>primary, secondary, danger, warning, info, light, dark</td>
				</tr>
				<tr>
					<th scope="row">outline</th>
					<td>Sets the outline on the button</td>
					<td>boolean</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="sizes">Sizes</h3>
		<p>
			Fancy larger or smaller buttons? Add the <code>size</code> prop with
			<code>lg</code> or <code>sm</code> for additional sizes.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary" size="lg">Primary</vui-button>
				<vui-button color="secondary" size="lg">Secondary</vui-button>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary" size="sm">Primary</vui-button>
				<vui-button color="secondary" size="sm">Secondary</vui-button>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">size</th>
					<td>Sets the size of the button</td>
					<td>sm, lg</td>
				</tr>
			</tbody>
		</table>
		<br>
		<p>
			Create block level buttonsthose that span the full width of a 
			parentby adding the <code>block</code> prop.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary" size="lg" block>Primary</vui-button>
				<vui-button color="secondary" size="lg" block>Secondary</vui-button>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">block</th>
					<td>Converts button to block level element</td>
					<td>boolean</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="active_state">Active State</h3>
		<p>
			Buttons will appear pressed (with a darker background, darker border, 
			and inset shadow) when active. <strong>Theres no need to add a class to 
			buttons as they use a pseudo-class</strong>. However, you can still force the 
			same active appearance with <code>active</code> prop should you need 
			to replicate the state programmatically.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary" size="lg" active>Primary Button</vui-button>
				<vui-button color="secondary" size="lg" active>Button</vui-button>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">active</th>
					<td>Sets the button's appearance to active</td>
					<td>boolean</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="disabled_state">Disabled State</h3>
		<p>
			Make buttons look inactive by adding the <code>disabled</code> prop 
			to any button element.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary" size="lg" disabled>Primary Button</vui-button>
				<vui-button color="secondary" size="lg" disabled>Button</vui-button>
			</template>
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">disabled</th>
					<td>Sets the button to be disabled</td>
					<td>boolean</td>
				</tr>
			</tbody>
		</table>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-button color="primary">Primary</vui-button>
<vui-button color="secondary">Secondary</vui-button>
<vui-button color="success">Success</vui-button>
<vui-button color="danger">Danger</vui-button>
<vui-button color="warning">Warning</vui-button>
<vui-button color="info">Info</vui-button>
<vui-button color="light">Light</vui-button>
<vui-button color="dark">Dark</vui-button>
`
const ex2 = `
<vui-button color="primary" outline>Primary</vui-button>
<vui-button color="secondary" outline>Secondary</vui-button>
<vui-button color="success" outline>Success</vui-button>
<vui-button color="danger" outline>Danger</vui-button>
<vui-button color="warning" outline>Warning</vui-button>
<vui-button color="info" outline>Info</vui-button>
<vui-button color="dark" outline>Dark</vui-button>
`

const ex3 = `
<vui-button color="primary" size="lg">Primary</vui-button>
<vui-button color="secondary" size="lg">Secondary</vui-button>
`

const ex4 = `
<vui-button color="primary" size="sm">Primary</vui-button>
<vui-button color="secondary" size="sm">Secondary</vui-button>
`

const ex5 = `
<vui-button color="primary" size="lg" block>Primary</vui-button>
<vui-button color="secondary" size="lg" block>Secondary</vui-button>
`

const ex6 = `
<vui-button color="primary" size="lg" active>Primary Button</vui-button>
<vui-button color="secondary" size="lg" active>Button</vui-button>
`

const ex7 = `
<vui-button color="primary" size="lg" disabled>Primary Button</vui-button>
<vui-button color="secondary" size="lg" disabled>Button</vui-button>
`

export default {
	name: 'Docs-Buttons',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Examples', link: 'examples' },
			{ label: 'Outline Button', link: 'outline_button' },
			{ label: 'Sizes', link: 'sizes' },
			{ label: 'Active State', link: 'active_state' },
			{ label: 'Disabled State', link: 'disabled_state' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


