<template>
	<div class="container">
		<h1 class="display-4">Data Table</h1>
		<p class="lead">
			Data tables display information in a way thats easy to scan, so that 
			users can look for patterns and insights.
		</p>
		<p>
			The Data Table was designed to be a starting point for you. Trying to acheive functionality
			that meets everyone's needs is a difficult task, so it was designed to give you flexibility
			over how you use it while being as easy to use as possible.
		</p>

		<h3 id="examples">Examples</h3>
		<p>
			For displaying tabular data. <code>&lt;dtable&gt;</code> supports pagination, filtering, 
			scrolling, themes, sorting, custom templates, events, and json data.
		</p>

		<br>

		<h4 id="basic">Basic Table</h4>
		<vui-code-panel>
			<template slot="example">
				<vui-dtable 
					id="dt0" 
					:rows="dt0.rows" 
					:headers="dt0.headers"
				/>
			</template>
			<template slot="code">{{ example0 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="table_props">Table Props</h3>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">id</th>
					<td>Required identifer for every table.</td>
				</tr>
				<tr>
					<th scope="row">:rows</th>
					<td>Array of row data objects.</td>
				</tr>
				<tr>
					<th scope="row">:headers</th>
					<td>Array of header data objects.</td>
				</tr>
				<tr>
					<th scope="row">color="value"</th>
					<td>Assigns colors to the entire table.</td>
				</tr>
				<tr>
					<th scope="row">headerColor="value"</th>
					<td>Assigns colors to just the table header.</td>
				</tr>
				<tr>
					<th scope="row">striped</th>
					<td>Adds zebra-striping to table rows.</td>
				</tr>
				<tr>
					<th scope="row">border</th>
					<td>Adds borders to columns and rows.</td>
				</tr>
				<tr>
					<th scope="row">borderless</th>
					<td>Removes all boders except for outer edges.</td>
				</tr>
				<tr>
					<th scope="row">hover</th>
					<td>Enables a hover state on table rows.</td>
				</tr>
				<tr>
					<th scope="row">small</th>
					<td>Make tables more compact by cutting cell padding in half.</td>
				</tr>
				<tr>
					<th scope="row">scroll</th>
					<td>Allows tables to be scrolled vertically.</td>
				</tr>
				<tr>
					<th scope="row">pageSize="value"</th>
					<td>Sets the number of rows that are visible.</td>
				</tr>
				<tr>
					<th scope="row">pagination</th>
					<td>
						Adds pagination functionality and automatically determines 
						the number of pages based on pageSize and total rows.
					</td>
				</tr>
				<tr>
					<th scope="row">filter</th>
					<td>Displays the filtering tool at the top of the table</td>
				</tr>
				<tr>
					<th scope="row">colShowHide</th>
					<td>
						Displays a dropdown button with a list of all the columns
						that can be toggles to show or hide.
					</td>
				</tr>
				<tr>
					<th scope="row">rowTemplate</th>
					<td>
						Tells the table that you are using a row template instead
						of the built in rows.
					</td>
				</tr>
				<tr>
					<th scope="row">editable</th>
					<td>
						Enables each row to be clicked and to display a basic form
						for editing row data. More information is provided in a later
						section of this page.
					</td>
				</tr>
				<tr>
					<th scope="row">@update="fnc()"</th>
					<td>
						Used in conjunction with <code>editable</code>. When you click 
						the submit button it emits this event so you can create your
						own function for handling updating the data. More information 
						is provided in a later section of this page.
					</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="header_data">Header Data</h3>
		<p>
			<code>:headers</code> is the table header column data, where each
			column is a set of keyed objects. Example format:
		</p>
		<vui-code-panel type="javascript">
			<template slot="code">{{ headerData }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Key</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Required</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">id</th>
					<td>Column identifier</td>
					<td>Yes</td>
				</tr>
				<tr>
					<th scope="row">value</th>
					<td>Column sort value. Lowecase value to sort the column by.</td>
					<td>Yes</td>
				</tr>
				<tr>
					<th scope="row">sortable</th>
					<td>Boolean value that sets if the column can be sorted.</td>
					<td>Yes</td>
				</tr>
				<tr>
					<th scope="row">selected</th>
					<td>Boolean value that sets if the column is displayed.</td>
					<td>Yes</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="row_data">Row Data</h3>
		<p>
			<code>:rows</code> is the table row data, where each
			row is a set of keyed objects. Example format:
		</p>
		<vui-code-panel type="javascript">
			<template slot="code">{{ rowData }}</template>
		</vui-code-panel>

		<br><br>

    <h3 id="scroll">Scrolling</h3>
		<p>
			Supports built in scrolling of data. You can control how many rows are 
			displayed at a time by setting the <code>pageSize</code> prop to the maximum number of 
			rows you would like displayed.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dtable 
					id="dt1" 
					:rows="dt1.rows" 
					:headers="dt1.headers"
					border
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="pagination">Pagination</h3>
		<p>
			Supports built in pagination of item data. You can control how many rows are 
			displayed at a time by setting the <code>pageSize</code> prop to the maximum number of 
			rows you would like displayed.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					pagination
				/>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="sorting">Sorting</h3>
		<p>
			You can make columns sortable. Clicking on a sortable column header will sort the column 
			in ascending direction, while clicking on it again will switch the direction of sorting.
			Columns that are sortable, but not yet sorted will display a <i class="fa icon-sort"></i>
			symbol. Columns that are sorted ascending will display a <i class="fa icon-caret-up"></i>
			symbol and descending will display a <i class="fa icon-caret-down"></i> symbol. The first 
			column will be sorted by default.
		</p>
		<p>
			To make columns <code>sortable</code>, you set the <code>sortable</code> key in the
			headers array object to <code>true</code>.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>	

		<br><br>

		<h3 id="filtering">Filtering</h3>
		<p>
			Filtering is acheived by adding the <code>filter</code> prop. Filtering is applied
			to individual columns. To filter by column you select the column from the 
			<code>Filter by</code> dropdown and then enter your search terms in the search field.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
					filter
				/>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>	

		<br><br>

		<h3 id="column_show_hide">Column Visibility</h3>
		<p>
			While it is possible to set the visibility of columns, by setting the 
			<code>headers selected</code> key to <code>true</code>, you can also
			expose this functionality to your users by adding the <code>colShowHide</code>
			prop. This will provide a built-in dropdown with a list of each column and a 
			checkbox that will toggle the columns visiblity. When a column is toggled to
			be hidden, it will also be hidden from the <code>Filter by</code> dropdown if
			it's being used in conjunction with filtering.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
					colShowHide
				/>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="editing">Row Editing</h3>
		<p>
			The data table provides basic built-in row editing capabilities. You enable 
			row editing by adding the <code>editable</code> prop. With editing enabled, you
			can click on a row and a popup form will be displayed.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
					editable
				/>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="update">Update Method</h4>
		<p>
			The data table provides an exposed update event that allows you to define
			your own custom functionality on how you want to handle updating your row
			data. The internal event is <code>@update="fnc()"</code>. 
		</p>
		<p>
			You will need to also add a snippet of code to the <code>mounted</code> hook. In order
			to have access to the the <code>update</code> event you have to access the <code>$root</code>
			instance. Then listen for the event using the <code>$on()</code> event handler.
		</p>
		<p>	
			This gives you access to the internal update event which is fired when the update button 
			in the form is clicked. In the callback you return a call to your custom method.
		</p>
		<vui-code-panel type="javascript">
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="row_template">Row Template</h3>
		<p>
			The row tempalte gives you the ability to customize the rows to fit your
			needs. To use a row template, start by adding the <code>rowTemplate</code> prop.
			This tells the table to disable the built-in rows.
		</p>
		<p>
			The example below shows the structure of the template using a <code>slot</code> with
			a <code>slot-scope</code>. The value of the slot must be <code>rowTemplate</code> and
			the value of the  <code>slot-scope</code> must be <code>props</code>. Also you must
			prepend each data property with <code>props.row</code>. Internally the table uses a 
			<code>v-for</code> loop and <code>row</code> is the required key name.
		</p>
		<p>
			For editable rows, the data table provides the <code>getRowData(props.row)</code> method,
			which calls an internall method for getting the selected row data and displaying the edit
			form.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
					editable
					rowTemplate
				>
					<template slot="rowTemplate" slot-scope="props">
						<tr @click="props.getRowData(props.row)">
							<td>{{ props.row.id }}</td>
							<td>{{ props.row.name }}</td>
							<td>{{ props.row.age }}</td>
							<td>{{ props.row.breed }}</td>
							<td>{{ props.row.gender | capFirst }}</td>
						</tr>
					</template>
				</vui-dtable>
			</template>
			<template slot="code">{{ example8 }}</template>
		</vui-code-panel>
		<br>
		<p>
			If you are using the <code>colShowHide</code> prop, for column visibility, you need
			to add a <code>v-show</code> directive to each column and call the internal method
			<code>showColumns()</code> with the column key as shown below:
		</p>
		<vui-code-panel type="javascript">
			<template slot="code">{{ example9 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="form_template">Form Template</h3>
		<p>
			The form template gives you the ability to customize the edit form to meet your 
			needs. In the example below a custom select has been added to the gender field of
			the form. You can learn more about the form components
			<router-link to="/docs/components/forms">here</router-link>.
		</p>
		<p>
			To use a form template, start by adding the <code>formTemplate</code> prop. The 
			structure of the template is just like the row template but with a diffrent slot
			name value. There are two diffrences with the form template. You prepend each form
			property with <code>props.selectedRow</code>. The form input component requires that 
			its prop type be a string.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt1" 
					:rows="dt1.rows" 
					:headers="dt1.headers"
					pageSize="5"
					scroll
					editable
					formTemplate
				>
					<template slot="formTemplate" slot-scope="props">
						<vui-form-group>
							<vui-form-label>Id</vui-form-label>
							<vui-input :value="String(props.selectedRow.id)" />
						</vui-form-group>
						<vui-form-group>
							<vui-form-label>Name</vui-form-label>
							<vui-input :value="String(props.selectedRow.name)" />
						</vui-form-group>
						<vui-form-group>
							<vui-form-label>Age</vui-form-label>
							<vui-input :value="String(props.selectedRow.age)" />
						</vui-form-group>
						<vui-form-group>
							<vui-form-label>Breed</vui-form-label>
							<vui-input :value="String(props.selectedRow.breed)" />
						</vui-form-group>
						<vui-form-group>
							<vui-form-label>Gender</vui-form-label>
							<vui-dropdown-select :value="props.selectedRow.gender">
								<vui-dropdown-select-option>
									{{ String(props.selectedRow.gender) }}
								</vui-dropdown-select-option>
								<vui-dropdown-select-option 
									v-for="(gender, index) in dt1.genders" 
									:key="index"
									v-if="gender !== props.selectedRow.gender"
								>
									{{ gender }}
								</vui-dropdown-select-option>
							</vui-dropdown-select>
						</vui-form-group>

						<vui-button @click="test()">Update</vui-button>
					</template>
				</vui-dtable> 
			</template>
			<template slot="code">{{ example10 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="styling">Table Styling</h3>
		<p>
			The following section covers each of the styling <code>props</code> that 
			are available for changing the appearance of the data table.
		</p>
		<br>

		<h3 id="table_color">Table Color</h3>
		<p>
			The entire table can be changed to any of the Bootstrap colors. To apply a 
			color to the table use the <code>color="value"</code> prop. When using an 
			editable table, the form header and update button 
			will also be changed to match the applied color. You can demo the colors below:
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<select class="form-control" v-model="tablecolor">
					<option value="primary" selected="selected">Primary</option>
					<option value="secondary">Secondary</option>
					<option value="success">Success</option>
					<option value="danger">Danger</option>
					<option value="warning">Warning</option>
					<option value="info">Info</option>
					<option value="light">Light</option>
					<option value="dark">Dark</option>
				</select>
				<hr>
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
					editable
					:color="tablecolor"
				/>
			</template>
			<template slot="code">{{ example11 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="header_color">Header Color</h3>
		<p>
			In addition to the whole table, the table header can be changed to any of the 
			Bootstrap colors. To apply a 
			color to the table use the <code>headerColor="value"</code> prop. When using an 
			editable table the edit form header and update button 
			will also be changed to match the applied color. You can demo the colors below:
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<select class="form-control" v-model="headercolor">
					<option value="primary" selected="selected">Primary</option>
					<option value="secondary">Secondary</option>
					<option value="success">Success</option>
					<option value="danger">Danger</option>
					<option value="warning">Warning</option>
					<option value="info">Info</option>
					<option value="light">Light</option>
					<option value="dark">Dark</option>
				</select>
				<hr>
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
					editable
					:headerColor="headercolor"
				/>
			</template>
			<template slot="code">{{ example12 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="bordered_table">Bordered Table</h3>
		<p>
			You have already seen the <code>border</code> prop in action. It has been used 
			in each of the examples on this page except for the Basic Table example.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					border
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example13 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="borderless_table">Borderless Table</h3>
		<p>
			Adding the <code>borderless</code> prop removes all borders except for the
			outer edges.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					borderless
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example14 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="striped_rows">Striped Rows</h3>
		<p>
			Add zebra-striping to any table row within the <code>&lt;tbody&gt;</code>.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					striped
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example15 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="hoverable_rows">Hoverable Rows</h3>
		<p>
			Add the <code>hover</code> prop to enable a hover state on table rows within 
			a <code>&lt;tbody&gt;</code>.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					hover
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example16 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="small_table">Small Table</h3>
		<p>
			Add the <code>small</code> prop to make tables more compact by cutting cell padding in half.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					small
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example17 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="table_shadow">Table Shadow</h3>
		<p>
			Use the <code>shadow</code> prop to add a drop shadow to the table.
		</p>
		<vui-code-panel type="javascript">
			<template slot="example">
				<vui-dtable 
					id="dt2" 
					:rows="dt2.rows" 
					:headers="dt2.headers"
					shadow
					pageSize="5"
					scroll
				/>
			</template>
			<template slot="code">{{ example18 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex0 = `
<vui-dtable 
	id="dt0" 
	:rows="dt0.rows" 
	:headers="dt0.headers"
/>
`
const headerData = `
dt0: {
	headers: [
		{ id: 0, value: 'id', label: 'ID', sortable: false, selected: true },
		{ id: 1, value: 'name', label: 'Name', sortable: false, selected: true },
		{ id: 2, value: 'age', label: 'Age', sortable: false, selected: true },
		{ id: 3, value: 'breed', label: 'Breed', sortable: false, selected: true },
		{ id: 4, value: 'gender', label: 'Gender', sortable: false, selected: true }
	],
	rows: []
}
`
const rowData = `
dt0: {
	headers: [],
	rows: [
		{ "id":0, "name":"Alpha", "age":2, "breed":"Calico", "gender":"male" },
