			{ label: 'Table Styling', link: 'styling' },
			{ label: '- Table Color', link: 'table_color' },
			{ label: '- Header Color', link: 'header_color' },
			{ label: '- Bordered Table', link: 'bordered_table' },
			{ label: '- Borderless Table', link: 'borderless_table' },
			{ label: '- Striped Rows', link: 'striped_rows' },
			{ label: '- Hoverable Rows', link: 'hoverable_rows' },
			{ label: '- Small Table', link: 'small_table' },
			{ label: '- Table Shadow', link: 'table_shadow' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


