<template>
	<div class="container">
		<h1 class="display-4">Shadows</h1>
		<p class="lead">
			Add or remove shadows to elements with the shadow utility directive.
		</p>

		<br>

		<h3 id="examples">Examples</h3>
		<p>
			While shadows on components are disabled by default in Bootstrap and can be enabled via .shadow classes, 
			you can also quickly add or remove a shadow with the shadow utility directive. 
			Includes support for shadow none and three default sizes.
		</p>
		<vui-code-panel>
			<template slot="example">
				<div v-shadow:none v-border.rounded v-color:bg="'light'" class="p-3 mb-5">No shadow</div>
				<div v-shadow:sm v-border.rounded v-color:bg="'light'" class="p-3 mb-5">Small shadow</div>
				<div v-shadow v-border.rounded v-color:bg="'light'" class="p-3 mb-5">Regular shadow</div>
				<div v-shadow:lg v-border.rounded v-color:bg="'light'" class="p-3 mb-5">Larger shadow</div>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<div v-shadow:none v-border.rounded v-color:bg="'light'" class="p-3 mb-5">No shadow</div>
<div v-shadow:sm v-border.rounded v-color:bg="'light'" class="p-3 mb-5">Small shadow</div>
<div v-shadow v-border.rounded v-color:bg="'light'" class="p-3 mb-5">Regular shadow</div>
<div v-shadow:lg v-border.rounded v-color:bg="'light'" class="p-3 mb-5">Larger shadow</div>
`

export default {
	name: 'Docs-Shadows',
	data() {
		return {
			example1: ex1
		}
	},
	mounted() {
		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


