<template>
	<div class="container">
		<h1 class="display-4">Text</h1>
		<p class="lead">
			Documentation and examples for common text utilities to control alignment, wrapping, weight, and more.
		</p>

		<br>

		<vui-callout>
			Vue has a <vui-code>v-text</vui-code> directive that updates an elements innerText. So the name
			used for this utility directive for working with text is <vui-code>v-font</vui-code>.
		</vui-callout>

		<br><br>

		<h3 id="text_aignment">Text Alignment</h3>
		<p>
			Easily realign text to components with text alignment. There are four alignment types:
		</p>
		<ul>
			<li><code>justify</code></li>
			<li><code>left</code></li>
			<li><code>center</code></li>
			<li><code>right</code></li>
		</ul>
		<vui-code-panel>
			<template slot="example">
				<p v-font:justify>
					Ambitioni dedisse scripsisse iudicaretur. Cras mattis iudicium purus 
					sit amet fermentum. Donec sed odio operae, eu vulputate felis rhoncus. 
					Praeterea iter est quasdam res quas ex communi. At nos hinc posthac, 
					sitientis piros Afros. Petierunt uti sibi concilium totius Galliae in 
					diem certam indicere. Cras mattis iudicium purus sit amet fermentum.
				</p>
				<hr>
				<div v-font:left>Left aligned text</div>
				<div v-font:center>Center aligned text</div>
				<div v-font:right>Right aligned text</div>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="text_overflow">Text Overflow</h3>
		<p>
			For longer content, you can truncate the text with an ellipsis. Requires <code>display: inline-block</code> 
			or <code>display: block</code>.
		</p>
		<vui-code-panel>
			<template slot="example">
				<div style="display:inline-block; max-width: 150px;" v-font:truncate>
					Ambitioni dedisse scripsisse iudicaretur. Cras mattis iudicium purus 
					sit amet fermentum. Donec sed odio operae, eu vulputate felis rhoncus. 
					Praeterea iter est quasdam res quas ex communi. At nos hinc posthac, 
					sitientis piros Afros. Petierunt uti sibi concilium totius Galliae in 
					diem certam indicere. Cras mattis iudicium purus sit amet fermentum.
				</div>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="text_transform">Text Transform</h3>
		<p>
			Transform text in components with these three text capitalization utilities:
		</p>
		<ul>
			<li><code>lcase</code>: Lowercase the entire string.</li>
			<li><code>ucase</code>: Uppercase the entire string.</li>
			<li><code>cap</code>: Capitilize only the first letter of each word.</li>
		</ul>
		<vui-code-panel>
			<template slot="example">
				<div v-font:lcase>Lowercased text</div>
				<div v-font:ucase>upppercase text</div>
				<div v-font:cap>capitilized text</div>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="text_weight">Text Weight & Italics</h3>
		<p>
			Quickly change the weight (boldness) of text or italicize text. There are four types:
		</p>
		<ul>
			<li><code>bold</code></li>
			<li><code>normal</code></li>
			<li><code>light</code></li>
			<li><code>italic</code></li>
		</ul>
		<vui-code-panel>
			<template slot="example">
				<p v-font:bold>Bold text.</p>
				<p v-font:normal>Normal weight text.</p>
				<p v-font:light>Light weight text.</p>
				<p v-font:italic>Italic text.</p>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="text_monospace">Text Monospace</h3>
		<p>
			Quickly change a selection to monospace.
		</p>
		<vui-code-panel>
			<template slot="example">
				<p v-font:monospace>This is in monospace</p>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<p v-font:justify>
	Ambitioni dedisse scripsisse iudicaretur. Cras mattis iudicium purus 
	sit amet fermentum. Donec sed odio operae, eu vulputate felis rhoncus. 
	Praeterea iter est quasdam res quas ex communi. At nos hinc posthac, 
	sitientis piros Afros. Petierunt uti sibi concilium totius Galliae in 
	diem certam indicere. Cras mattis iudicium purus sit amet fermentum.
</p>
<hr>
<div v-font:left>Left aligned text</div>
<div v-font:center>Center aligned text</div>
<div v-font:right>Right aligned text</div>
`
const ex2 = `
<div style="display:inline-block; max-width: 150px;" v-font:truncate>
	Ambitioni dedisse scripsisse iudicaretur. Cras mattis iudicium purus 
	sit amet fermentum. Donec sed odio operae, eu vulputate felis rhoncus. 
	Praeterea iter est quasdam res quas ex communi. At nos hinc posthac, 
	sitientis piros Afros. Petierunt uti sibi concilium totius Galliae in 
	diem certam indicere. Cras mattis iudicium purus sit amet fermentum.
</div>
`
const ex3 = `
<div v-font:lcase>Lowercased text</div>
<div v-font:ucase>upppercase text</div>
<div v-font:cap>capitilized text</div>
`
const ex4 = `
<p v-font:bold>Bold text.</p>
<p v-font:normal>Normal weight text.</p>
<p v-font:light>Light weight text.</p>
<p v-font:italic>Italic text.</p>
`
const ex5 = `
<p v-font:monospace>This is in monospace</p>
`

export default {
	name: 'Docs-Text',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Text Alignment', link: 'text_alignment' },
			{ label: 'Text Overflow', link: 'text_overflow' },
			{ label: 'Text Transform', link: 'text_transform' },
			{ label: 'Text Weight & Italics', link: 'text_weight' },
			{ label: 'Text Monospace', link: 'text_monospace' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


