<template>
	<div class="container">
		<h1 class="display-4">Breadcrumb</h1>
		<p class="lead">
			Indicate the current pages location within a navigational 
			hierarchy that automatically adds separators.
		</p>

		<h3 id="example">Example</h3>
		<p>
			The breadcrumb component is intended to be used with the Vue-Router.
			It will automatically generate crumbs based on the current route and
			adds links to each path segment.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-breadcrumb />
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="seperators">Seperators</h3>
		<p>
			By default, breadcrumbs use a text divider. You can change the divider
			by setting the <code>seperator</code> prop as show below:
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-breadcrumb seperator=">" />
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">seperator</th>
					<td>Sets the seperator to use.</td>
					<td>any text item.</td>
				</tr>
			</tbody>
		</table>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-breadcrumb />
`
const ex2 = `
<vui-breadcrumb seperator=">" />
`

const ex3 = `

`

const ex4 = `

`

export default {
	name: 'Docs-Breadcrumb',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Example', link: 'example' },
			{ label: 'Seperators', link: 'seperators' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


