<template>
	<div class="container">
		<h1 class="display-4">Pagination</h1>
		<p class="lead">
			Documentation and examples for showing pagination to indicate a 
			series of related content exists across multiple pages.
		</p>

		<br>

		<h3 id="overview">Overview</h3>
		<p>
			The <code>vui-pagination</code> component uses a large block of connected links for 
			pagination, making links hard to miss and easily scalable  all while providing large hit 
			areas. The <code>vui-pagination</code> component has several customizable features that
			can be set via props.
		</p>
		<br>
		<table class="table table-bordered table-striped">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Description</th>
					<th scope="col">Value</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row" width="120"><code>totalPages</code></th>
					<td>Total number of items to be paginated.</td>
					<td>Number</td>
				</tr>
				<tr>
					<th scope="row"><code>align</code></th>
					<td>Positions the component hozizontally.</td>
					<td>left, center, right</td>
				</tr>
				<tr>
					<th scope="row"><code>size</code></th>
					<td>Sets the size of the component.</td>
					<td>sm, md, lg</td>
				</tr>
				<tr>
					<th scope="row"><code>prevText</code></th>
					<td>
						Label for the Previous button (&lsaquo;).
					</td>
					<td>Previous, Prev, &lsaquo;</td>
				</tr>
				<tr>
					<th scope="row"><code>nextText</code></th>
					<td>
						Label for the Next button (&rsaquo;).
					</td>
					<td>Next, &rsaquo;</td>
				</tr>
				<tr>
					<th scope="row"><code>color</code></th>
					<td>
						Sets the background color of the active pager.
					</td>
					<td>primary, secondary, success, danger, warning, dark</td>
				</tr>
				<tr>
					<th scope="row"><code>currentPage</code></th>
					<td>
						Sets the active pager to be displayed.
					</td>
					<td>Any page number</td>
				</tr>
			</tbody>
		</table>

		<br>

		<h3 id="events">Events</h3>
		<table class="table table-bordered table-striped">
			<thead>
				<tr>
					<th scope="col">Event</th>
					<th scope="col">Description</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row" width="120"><code>pageChange</code></th>
					<td>
						Emits two values <br>
						<code>page</code>: the current selected page <br>
						<code>pages</code>: the total number of pages
					</td>
				</tr>
			</tbody>
		</table>
		<vui-code-panel>
			<template slot="code">{{ overview1 }}</template>
		</vui-code-panel>
		<vui-code-panel type="javascript">
			<template slot="code">{{ overview2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="examples">Examples</h3>
		<p>
			
		</p>

		<br>

		<h4 id="button_text">Button Text</h4>
		<p>
			You can use text or symbols to represent Previous and Next, based
			on your needs, by adding the <code>prevText</code> and <code>nextText</code>
			props and changing there values to plain text or any unicode symbol. 
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-pagination :totalPages="100" @pageChange="test" />
				<vui-pagination 
					:totalPages="100"
					prevText="&lsaquo;" 
					nextText="&rsaquo;"
					@pageChange="test" 
				/>
				<hr>
				Showing page {{ currentPage }} of {{ totalPages }}
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="sizing">Sizing</h4>
		<p>
			You can change the size of the pagination component with the <code>size</code>
			prop. The default pagination component size is medium (<code>md</code>). You
			can make it smaller (<code>sm</code>) or lager (<code>lg</code>), by setting
			the prop to the appropriate value.
		</p>
		<vui-code-panel>
			<template slot="example">
				<strong>Small</strong>
				<vui-pagination :totalPages="100" size="sm" />
				<hr>
				<strong>Medium (Default)</strong>
				<vui-pagination :totalPages="100" size="md" />
				<hr>
				<strong>Large</strong>
				<vui-pagination :totalPages="100" size="lg" />
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="alignment">Alignment</h4>
		<p>
			Change the alignment of pagination components with the <code>size</code> prop.
			By default the pagination component is center aligned. Change the alignment 
			to left or right.
		</p>
		<vui-code-panel>
			<template slot="example">
				<strong>Left alignment</strong><br><br>
				<vui-pagination :totalPages="100" align="left" />
				<hr>
				<strong>Center alignment (Default)</strong><br><br>
				<vui-pagination :totalPages="100" />
				<hr>
				<strong>Right alignment</strong><br><br>
				<vui-pagination :totalPages="100" align="right" />
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>


		<br><br>

		<h4 id="colors">Colors</h4>
		<p>
			The default color for the active page is <code>primary</code>. You 
			can change this by setting the <code>color</code> prop to the appropriate value. 
		</p>
		<vui-code-panel>
			<template slot="example">
				<strong>Primary (Default)</strong>
				<vui-pagination :totalPages="100" />
				<hr>
				<strong>Secondary</strong>
				<vui-pagination :totalPages="100" color="secondary" />
				<hr>
					<strong>Success</strong>
				<vui-pagination :totalPages="100" color="success" />
				<hr>
				<strong>Danger</strong>
				<vui-pagination :totalPages="100" color="danger" />
				<hr>
				<strong>Warning</strong>
				<vui-pagination :totalPages="100" color="warning" />
				<hr>
				<strong>Info</strong>
				<vui-pagination :totalPages="100" color="info" />
				<hr>
				<strong>Dark</strong>
				<vui-pagination :totalPages="100" color="dark" />
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="current_page">Current Page</h4>
		<p>
			By default the current page is set to 1. You can override this setting via
			the <code>currentPage</code> prop.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-pagination :totalPages="100" :currentPage="10" @pageChange="test" />
				<hr>
				Showing page {{ currentPage }} of {{ totalPages }}
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="ellipsis">Ellipsis</h4>
		<p>
			Ellipsis will be displayed when you have more than 10 pages.
		</p>
		<vui-code-panel>
			<template slot="example">
				<h6>When you have 10 pages or less</h6>
				<vui-pagination :totalPages="10" :currentPage="1" @pageChange="test" />
				<hr>
				<h6>When you have more than 10 pages</h6>
				<vui-pagination :totalPages="100" :currentPage="1" @pageChange="test" />
				<hr>
				Showing page {{ currentPage }} of {{ totalPages }}
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>


		<br><br><br><br>
	</div>
</template>

<script>
const ov1 = `
<vui-pagination :totalPages="100" @pageChange="test" />	
<hr>
Showing page {{ currentPage }} of {{ totalPages }}
`
const ov2 = `
export default {
  data () {
    return {
      currentPage: 1,
			totalPages: null
    }
  },
	methods: {
		test(data) {
			this.currentPage = data.page
			this.totalPages = data.pages
		}
	}
}
`

const ex1 = `
<vui-pagination :totalPages="100" />
				
<vui-pagination :totalPages="100" prevText="&lsaquo;" nextText="&rsaquo;" />
`
const ex2 = `
<strong>Small</strong>
<vui-pagination :totalPages="100" size="sm" />

<hr>

<strong>Medium (Default)</strong>
<vui-pagination :totalPages="100" size="md" />

<hr>

<strong>Large</strong>
<vui-pagination :totalPages="100" size="lg" />
`
const ex3 = `
<strong>Left alignment</strong>
<vui-pagination :totalPages="100" align="left" />

<hr>

<strong>Center alignment (Default)</strong>
<vui-pagination :totalPages="100" />

<hr>

<strong>Right alignment</strong>
<vui-pagination :totalPages="100" align="right" />
`
const ex4 = `
<strong>Primary (Default)</strong>
<vui-pagination :totalPages="100" />
<hr>
<strong>Secondary</strong>
<vui-pagination :totalPages="100" color="secondary" />
<hr>
	<strong>Success</strong>
<vui-pagination :totalPages="100" color="success" />
<hr>
<strong>Danger</strong>
<vui-pagination :totalPages="100" color="danger" />
<hr>
<strong>Warning</strong>
<vui-pagination :totalPages="100" color="warning" />
<hr>
<strong>Info</strong>
<vui-pagination :totalPages="100" color="info" />
<hr>
<strong>Dark</strong>
<vui-pagination :totalPages="100" color="dark" />
`
const ex5 = `
<!-- 
You must bind props that pass numbers as strings, 
this casts the value to a number 
-->
<vui-pagination :totalPages="100" :currentPage="10" @pageChange="test" />
<hr>
Showing page {{ currentPage }} of {{ totalPages }}
`
const ex6 = `
<vui-pagination :totalPages="10" :currentPage="1" @pageChange="test" />

<vui-pagination :totalPages="100" :currentPage="1" @pageChange="test" />
`
const ex7 = `

`
const ex8 = `

`
const ex9 = `

`

export default {
	name: 'Docs-Pagination',
	data() {
		return {
			overview1: ov1,
			overview2: ov2,
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8,
			example9: ex9,

			currentPage: 1,
			totalPages: null
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Overview', link: 'overview' },
			{ label: 'Events', link: 'events' },
			{ label: 'Examples', link: 'examples' },
			{ label: '- Button Text', link: 'button_text' },
			{ label: '- Sizing', link: 'sizing' },
			{ label: '- Alignment', link: 'alignment' },
			{ label: '- Colors', link: 'colors' },
			{ label: '- Current Page', link: 'current_page' },
			{ label: '- Ellipsis', link: 'ellipsis' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		},
		test(data) {
			this.currentPage = data.page
			this.totalPages = data.pages
		}
	}
}
</script>


