<template>
	<div id="docs">
		<!-- Sidebar -->
		<div id="search-wrapper">
			<a href="https://www.patreon.com/joe_lomoglio" target="_blank">
				<img src="./../../../static/images/patron_logo.png" width="170px" />
			</a>
		</div>
		<div id="sidebar-wrapper">
			<vui-accordion>
				<vui-accordion-panel 
					header="Getting Started" 
					@click="setRoute('getting-started','intro')" 
					:active="isGettingStartedActive"
				>
					<ul class="sidebar-nav">
						<li><router-link to="/docs/getting-started/intro" exact>Introduction</router-link></li>
						<li><router-link to="/docs/getting-started/download">Download & Setup</router-link></li>
						<li><router-link to="/docs/getting-started/roadmap">Roadmap</router-link></li>
					</ul>
				</vui-accordion-panel>

				<vui-accordion-panel 
					header="Components" 
					@click="setRoute('components','accordion')" 
					:active="isComponentsActive"
				>
					<ul class="sidebar-nav">
						<li><router-link to="/docs/components/accordion">Accordion</router-link></li>
						<li><router-link to="/docs/components/alerts">Alerts</router-link></li>
						<li><router-link to="/docs/components/badge">Badge</router-link></li>
						<li><router-link to="/docs/components/breadcrumb">Breadcrumb</router-link></li>
						<li><router-link to="/docs/components/buttons">Buttons</router-link></li>
						<li><router-link to="/docs/components/button_group">Button Group</router-link></li>
						<li><router-link to="/docs/components/callout">Callout</router-link></li>
						<li><router-link to="/docs/components/card">Card</router-link></li>
						<li><router-link to="/docs/components/code_panel">Code Panel</router-link></li>
						<li><router-link to="/docs/components/data_table">Data Table</router-link></li>
						<li><router-link to="/docs/components/dropdowns">Dropdowns</router-link></li>
						<li><router-link to="/docs/components/forms">Forms</router-link></li>
						<li><router-link to="/docs/components/icon">Icon</router-link></li>
						<li><router-link to="/docs/components/image_placeholder">Image Placeholder</router-link></li>
						<li><router-link to="/docs/components/input_group">Input Group</router-link></li>
						<li><router-link to="/docs/components/jumbotron">Jumbotron</router-link></li>
						<li><router-link to="/docs/components/list_group">List Group</router-link></li>
						<li><router-link to="/docs/components/modal">Modal</router-link></li>
						<li><router-link to="/docs/components/navs">Navs</router-link></li>
						<li><router-link to="/docs/components/navbar">Navbar</router-link></li>
						<li><router-link to="/docs/components/pagination">Pagination</router-link></li>
						<li><router-link to="/docs/components/progress_bars">Progress Bars</router-link></li>
						<li><router-link to="/docs/components/tooltips">Tooltips</router-link></li>
					</ul>
				</vui-accordion-panel>

				<!--
				<vui-accordion-panel header="Widgets">
					<ul class="sidebar-nav">
						<li><router-link to="/">Login Panel</router-link></li>
						<li><router-link to="/">Tab Panel</router-link></li>
						<li><router-link to="/">Code Panel</router-link></li>
						<li><router-link to="/">Sidenav</router-link></li>
						<li><router-link to="/">Toolbar</router-link></li>
					</ul>
				</vui-accordion-panel>
				-->

				<vui-accordion-panel 
					header="Utilities"
					@click="setRoute('utilities','borders')" 
					:active="isUtilitiesActive"
				>
					<ul class="sidebar-nav">
						<li><router-link to="/docs/utilities/borders">Borders</router-link></li>
						<li><router-link to="/docs/utilities/colors">Colors</router-link></li>
						<li><router-link to="/docs/utilities/draggable">Draggable</router-link></li>
						<li><router-link to="/docs/utilities/float">Float</router-link></li>
						<li><router-link to="/docs/utilities/shadows">Shadows</router-link></li>
						<li><router-link to="/docs/utilities/text">Text</router-link></li>
						<li><router-link to="/docs/utilities/vertical_align">Vertical Align</router-link></li>
					</ul>
				</vui-accordion-panel>
			</vui-accordion>
		</div>
		<!-- /Sidebar -->

		<!-- Page Content -->
		<div id="page-content-wrapper">
			<div id="toc-wrapper" v-if="tocLinks.length >= 1">
				<ul class="toc">
					<li 
					  v-for="(item, index) in tocLinks" 
					  :key="index" 
						@click="$refs.view.scrollTo(item.link)"
					>
						{{ item.label }}
					</li>
				</ul>

				<small>Silver Sponsors</small>
			</div>
			<div id="page-content">
				<router-view ref="view" />
			</div>
		</div>
		<!-- /Page Content -->

    </div>
</template>

<script>
export default {
	name: 'docs',
	data() {
		return {
			tocLinks: []
		}
	},
	mounted() {
		// Added becasue could not get router-link to maintain
		// active class to match on the route
		$(document).find('#docLink').addClass('active')	
	},
	computed: {
		isGettingStartedActive() {
			return this.$route.path.includes('getting-started') ? true : false
		},
		isComponentsActive() {
			return this.$route.path.includes('components') ? true : false
		},
		isUtilitiesActive() {
			return this.$route.path.includes('utilities') ? true : false
		}
	},
	methods: {
		setRoute(category, topic) {
			this.$router.push({ path: `/docs/${category}/${topic}` })
		}
	}
}
</script>

<style lang="scss">
#search-wrapper {
	position: fixed;
	top: 55px;
  left: 0px;
  width: 200px;
	padding: 10px;
	padding-top: 15px;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}

#sidebar-wrapper {
	position: fixed;
	top: 120px;
  left: 0px;
  width: 200px;
  bottom: 0;
	overflow-y: auto;
	overflow-x: hidden;
  background: #fff;
	border-right: 1px solid #ccc;
	padding-bottom: 30px;

	/* Sidebar Styles */
	.sidebar-nav {
		position: relative;
		width: 200px;
		margin: 0;
		margin-top: -18px;
		padding: 0;
		list-style: none;
	}

	.sidebar-nav li {
		text-indent: 0px;
		line-height: 30px;
	}

	.sidebar-nav li a {
		display: block;
		text-decoration: none;
		color: #5d6168;
		font-weight: 200;
		font-size: 14px;
	}

	.sidebar-nav li a.active {
		display: block;
		text-decoration: none;
		color: rgba(0,0,0,.65);
		font-weight: 700;
	}

	.sidebar-nav li a:hover {
		text-decoration: none;
		color: dimgray;
		background: rgba(255, 255, 255, 0.2);
	}

	.sidebar-nav li a:active, .sidebar-nav li a:focus {
		text-decoration: none;
	}

	.sidebar-nav>.sidebar-brand {
		height: 65px;
		font-size: 18px;
		line-height: 60px;
	}

	.sidebar-nav>.sidebar-brand a {
		color: #999999;
	}

	.sidebar-nav>.sidebar-brand a:hover {
		color: #333;
		background: none;
	}

	.card {
		border: 0px solid #fff !important;
	}
	.bg-light {
    background-color: #fff !important;
	}
	.card-header {
		border-bottom: 0px solid #fff !important;
		height: 36px;
	}
	.card-header.bg-light {
		background-color: #fff !important;
	}
	.card-header h6 {
		font-size: 17px;
		font-weight: 200;
	}
	.card-body {
		margin-bottom: -20px;
	}
}

#page-content-wrapper {
  position: fixed;
	top: 55px;
	left: 200px;
	right: 0;
	bottom: 0;
	padding-top: 40px;
	padding-left: 30px;
	padding-right: 40px;
	overflow-y: auto;

	#page-content {
		position: absolute;
		top: 40px;
		left: 30px;
		right: 160px;
		bottom: 0;
		padding-right: 35px;
	}

	#toc-wrapper {
		position: fixed;
		top: 80px;
		right: 0;
		width: 190px;
		padding-top: 40px;
		padding-left: 10px;
		padding-right: 20px;
		border-left: 1px solid #ccc;

		.toc {
			position: relative;
			width: 160px;
			margin-left: -10px !important;
			margin-top: -40px;
			padding: 0;
			list-style: none;
		}

		.toc li {
			padding-left: 10px;
			border-left: 2px solid white;
			cursor: pointer;
			font-size: 14px !important;
			font-weight: 200 !important;
			line-height: 22px !important;
			color: #6c757d;
		}
		.toc li:hover {
			border-left: 2px solid blue;
		}
		.toc li.active {
			border-left: 2px solid green;
		}
	}
}
</style>


