		<h1 class="display-4">Navbar</h1>
		<p class="lead">
			Documentation and examples for Vuis powerful, responsive navigation header, the vui-navbar. 
			Includes support for branding, navigation, and more, including support for responsive behaviors.
		</p>

		<br>

		<h3 id="how_it_works">How it works</h3>
		<p>
			Heres what you need to know before getting started with the vui-navbar:
		</p>
		<ul>
			<li>
				Navbars require the <code>expand</code> prop for responsive collapsing. <br>
				Values for expand are: <code>sm | md | lg | xl</code>
			</li>
			<li>
				Navbars and their contents are fluid by default. Use optional 
				containers to limit their horizontal width.
			</li>
			<li>
				Use Bootstrap's spacing and flex utility classes for controlling 
				spacing and alignment within navbars.
			</li>
		</ul>
		<p>Read on for an example and list of supported sub-components.</p>

		<br>

		<h3 id="supported_content">Supported Content</h3>
		<p>
			Navbars come with built-in support for a handful of sub-components. 
			Choose from the following as needed:
		</p>
		<ul>
			<li>
				<code>vui-navbar-brand</code> for your comapny, product, or project name.
			</li>
			<li>
				<code>vui-navbar-nav</code> for a full-height and lightweight navigation 
				(including support for dropdowns).
			</li>
			<li>
				<code>vui-navbar-toggler</code> for use with the <code>vui-navbar-collapse</code> 
				sub-component.
			</li>
			<li>
				<code>vui-navbar-collapse</code> automatically collapses at the desfined breakpoint.
			</li>
			<li>
				<code>vui-navbar-item</code> for <code>router-links</code>. The <code>vui-navbar</code> was designed to be used
				in SPA (Single Page Applications) that use the Vue Router.
			</li>
			<li>
				<code>vui-navbar-item-dropdown</code> a dropdown menu specifically for use with the navbar.
			</li>
			<li>
				<code>vui-dropdown-item</code> for use with the <code>vui-navbar-item-dropdown</code>.
			</li>
		</ul>

		<p>
			Heres an example of all the sub-components included in a responsive light-themed navbar that 
			automatically collapses at the lg (large) breakpoint.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-navbar expand="lg" theme="light" color="light">
					<vui-navbar-brand to="/docs/components/navbar">Navbar</vui-navbar-brand>
					<vui-navbar-toggler />
					<vui-navbar-collapse>
						<vui-navbar-nav>
							<vui-navbar-item to="/docs/components/navbar" active>Home</vui-navbar-item>
							<vui-navbar-item to="/docs/components/navbar">Link</vui-navbar-item>
							<vui-navbar-item-dropdown text="Dropdown" autoClose>
								<vui-dropdown-item to="/docs/components/navbar">I'm a router-link</vui-dropdown-item>
								<vui-dropdown-item to="/docs/components/navbar">I'm a router-link</vui-dropdown-item>
								<vui-dropdown-item to="/docs/components/navbar">I'm a router-link</vui-dropdown-item>
								<vui-dropdown-divider />
								<vui-dropdown-item href="/docs/components/navbar">I'm a standard link</vui-dropdown-item>
