		<br><br>

		<h4 id="brand">Brand</h4>
		<p>
			The <code>vui-navbar-brand</code> is a router-link wrapper that can contain most elements. 
			You override using it as a link by adding the <code>heading</code> prop. Using the
			<code>heading</code> prop, removes the router-link.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-navbar expand="lg" theme="light" color="light">
					<vui-navbar-brand to="/docs/components/navbar">Navbar</vui-navbar-brand>
				</vui-navbar>
				<vui-navbar expand="lg" theme="light" color="light">
					<vui-navbar-brand heading>Navbar</vui-navbar-brand>
				</vui-navbar>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<br>
		<p>
			Adding images to the <code>vui-navbar-brand</code> requires no special styles
