			Navbar navigation links build on the <code>vui-navbar-nav</code> parent component and requires 
			the use of <code>vui-navbar-collapse</code> and <code>vui-navbar-toggler</code> for proper 
			responsive functionality. Navigation in navbars will also grow to occupy as much horizontal 
			space as possible to keep your navbar contents securely aligned.
		</p>
		<p>It supports the following components:</p>
		<ul>
			<li>
				<code>vui-navbar-item</code> for <code>router-links</code>. 
			</li>
			<li>
				<code>vui-navbar-item-dropdown</code> a dropdown menu specifically for use with the navbar.
			</li>
			<li>
				<code>vui-dropdown-item</code> for adding router-links or standard links to the 
				<code>vui-navbar-item-dropdown</code>.
			</li>
			<li>
				<code>vui-form</code> for adding simple forms to the navbar.
			</li>
		</ul>

		<br>

		<h4 id="navbar_item">Navbar Item</h4>
		<p>
			The <code>vui-navbar-item</code> is the main component for creating <code>router-links</code>.
			You need to add the required <code>to=""</code> prop. Vui was designed for SPA
			(Single Page Applications) so it only provides <code>router-links</code> for main navigation items.
		</p>
		<vui-code-panel>
			<template slot="example"></template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="navbar_dropdown">Navbar Item Dropdown</h4>
		<p>
			The <code>vui-navbar-item-dropdown</code> is similar to the <code>vui-dropdown-button</code> The
			diffrence is that it is specifically designed to only work with the <code>vui-navbar-nav</code>.
		</p>
		<p>The <code>vui-navbar-item-dropdown</code> has two available props:</p>
		<ul>
			<li>
				<code>text</code> for displaying the text for the dropdown link.
			</li>
			<li>
				<code>autoClose</code> which toggles the dropdown 
				menu to automatically close when a <code>vui-dropdown-item</code> is clicked. 
			</li>
		</ul>

		<p>The <code>vui-navbar-item-dropdown</code> has three available components:</p>
		<ul>
			<li>
				<code>vui-navbar-item</code> for link and router-link items.
			</li>
			<li>
				<code>vui-dropdown-divider</code> for separating groups of related menu items.
			</li>
			<li>
				<code>vui-dropdown-item-text</code> for adding strings of vertically centered text.
			</li>
		</ul>
		<vui-code-panel>
