		<br><br>

		<h3 id="color_schemes">Color Schemes</h3>
		<p>
			Theming the <vui-code>vui-navbar</vui-code> is simple thanks to the combination of theming 
			classes and background-color utilities. Choose from <vui-code>light</vui-code> for use with 
			light background colors, or <vui-code>dark</vui-code> for dark background colors. Then, 
			customize with the following props:
		</p>
		<ul>
			<li>
				<vui-code>color</vui-code> sets the background-color when using Bootstrap colors.
			</li>
			<li>
				<vui-code>bgColor</vui-code> sets the background-color for custom colors.
			</li>
		</ul>
		<p>
			When using a <vui-code>vui-button</vui-code> you will need to add the <vui-code>theme</vui-code>
			prop and applly the same theme color as you applied to the <vui-code>vui-navbar</vui-code>. The
			buttons <vui-code>color</vui-code> prop sets the buttons background-color. See the 
			<router-link to="/docs/components/buttons">Buttons</router-link> section for more information 
			on <vui-code>vui-buttons</vui-code>.
		</p>
		<p>
			You are not limited to only the Bootstrap colors, you can apply any color you want.
			To apply custom colors to the <vui-code>vui-navbar</vui-code> you need to replace 
			the <vui-code>color</vui-code> prop with the <vui-code>bgColor</vui-code> prop. 
