<template>
	<div class="container">
		<h1 class="display-4">Modal</h1>
		<p class="lead">
			Modals are one of the most ubiquitous UI elements on the web. They communicate information 
			via a secondary window and allow the user to maintain the context of a particular task.
		</p>
		<p>
			There are a wide range of use cases from user notification to custom content and they feature 
			an array of helpful sub-components, sizes, colors and more.
		</p>

		<h3 id="overview">Overview</h3>
		<p>
			By default the modal is vertically centered, this can be changed by setting various props.
			Also by default the modal has <code>Cancel</code> and <code>Ok</code> buttons in the footer.
			The footer and header can be customized through the use of templates. Default button actions
			emit events that you can use to hide/show and apply custom actions. Clicking on the modal "backdrop"
			will not automatically close the modal.
		</p>
		<p>
			The default modal title can be changed by setting the <code>title</code> and <code>icon</code>
			props. When using the custom templates for header and footer, the default elements will not 
			be displayed.
		</p>
		<p>
			Modals use <code>position: fixed</code>, which can sometimes be a bit particular about its rendering. 
			Whenever possible, place your modal HTML in a top-level position to avoid potential interference 
			from other elements. Youll likely run into issues when nesting a modal within another fixed element.
		</p>
		<p>
			Modals use percentages for their vertical positioning and sizing. Using pixel values will not work.
		</p>

		<br><br>

		<h3 id="example">Example</h3>
		<p>
			Below is a static modal example (meaning its position and display have been overridden). 
			Included are the modal header, modal body (required for padding), and modal footer (optional). 
		</p>
		<vui-code-panel>
			<template slot="example">
				<div class="card shadow rounded w-75">
					<div class="card-header">
						<div class="row">
							<div class="col-sm-6">
								<h5 class="modal-title">
									<i class="icon-warning-sign mr-1"></i> Modal Title
								</h5>
							</div>
							<div class="col-sm-6">
								<i class="icon-remove float-right modal-close"></i>
							</div>
						</div>
					</div>
					<div class="card-body card-text">
						Modal body text goes here.
					</div>
					<div class="card-footer text-muted text-right">
						<vui-button color="secondary">Cancel</vui-button>
						<vui-button color="primary">Ok</vui-button>
					</div>
				</div>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="events">Default Events</h3>
		<p>
			By default the modal will emit two events: <code>close</code> and <code>ok</code>. The 
			<code>close</code> is triggred by the &times; icon and the <code>Cancel Button</code>.
			The <code>ok</code> event is triggred by the <code>Ok Button</code>
		</p>
		<vui-code-panel type="javascript">
			<template slot="code">{{ example1a }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="toggle_visibility">Toggle Visibility</h3>
		<p>
			The default internal close icon and cancel button emit a <code>close</code> event that you
			can use to provide your own logic for hiding and showing the modal. An example 
			of how to acheive this is shown below:
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					title="Modal Title"
					position="center" 
					v-show="showModal1" 
					@close="showModal1 = !showModal1"
				>
					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal1 = !showModal1"
				>
					Show Modal
				</vui-button>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="header_template">Header Template</h3>
		<p>
			Using the <code>header template</code> allows you to customize it to fit
			your needs.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					position="center" 
					v-show="showModal2" 
					@close="showModal2 = !showModal2"
					headerTemplate
				>
					<template slot="header">
						<div class="d-flex justify-content-center">
							<h4>Template Modal Title</h4>
						</div>
					</template>

					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal2 = !showModal2"
				>
					Show Modal
				</vui-button>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="footer_template">Footer Template</h3>
		<p>
			Using the <code>footer template</code> allows you to customize it to fit
			your needs.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					position="center" 
					v-show="showModal3" 
					@close="showModal3 = !showModal3"
					footerTemplate
				>
					<template slot="footer">
						<vui-button @click="showModal3 = !showModal3">Cancel</vui-button>
						<vui-button color="primary">Ok</vui-button>
					</template>

					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal3 = !showModal3"
				>
					Show Modal
				</vui-button>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="vertical_position">Vertical Position</h3>
		<p>
			The modal provides two vertical postions, the default <code>center</code> and a <code>top</code>
			position. You have already seen the <code>center</code> position in use, this example demonstrates
			the <code>top</code> position.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					position="top"
					topOffset="5%" 
					v-show="showModal4" 
					@close="showModal4 = !showModal4"
				>
					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal4 = !showModal4"
				>
					Show Modal
				</vui-button>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>
		<br>
		<p>
			When setting the postion to <code>top</code> you can adjust the offset by using the 
			<code>topOffset="value"</code> prop. It takes percentage values. In the example below, the
			<code>topOffset</code> is set to <code>15</code>. Values are percentages passed in as a number 
			only without the <code>%</code> symbol.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					position="top"
					topOffset="15" 
					v-show="showModal5" 
					@close="showModal5 = !showModal5"
				>
					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal5 = !showModal5"
				>
					Show Modal 
				</vui-button>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="sizing">Sizing</h3>
		<p>
			Modals will automatically ajust their height based on the content in the body section of 
			the modal. You can adjust the overall with of the modal by applying the <code>width="value"</code>
			prop. Values are percentages passed in as a number only without the <code>%</code> symbol. 
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					position="center"
					width="40"
					v-show="showModal6" 
					@close="showModal6 = !showModal6"
				>
					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal6 = !showModal6"
				>
					Show Modal
				</vui-button>
			</template>
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="colors">Colors</h3>
		<p>
			You can change the default background color of the header, by appying the 
			<code>color="value"</code> prop. The header accepts any of the default
			Bootstrap colors.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					position="center"
					color="primary"
					v-show="showModal7" 
					@close="showModal7 = !showModal7"
				>
					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal7 = !showModal7"
				>
					Show Modal
				</vui-button>
			</template>
			<template slot="code">{{ example8 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="footer">Disable Footer</h3>
		<p>
			There may be cases where you do not want the modal to have a footer. You 
			can disable the footer by applying the <code>noFooter</code> prop.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-modal
					position="center"
					v-show="showModal8" 
					@close="showModal8 = !showModal8"
					noFooter
				>
					<h5>Vertically Centered Modal</h5>
					<p>Modal body content goes here.</p>
				</vui-modal>

				<vui-button 
					type="text" 
					color="secondary" 
					outline 
					@click="showModal8 = !showModal8"
				>
					Show Modal
				</vui-button>
			</template>
			<template slot="code">{{ example9 }}</template>
		</vui-code-panel>

		<br><br>
		
		<h3 id="props">Modal Props</h3>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">icon</th>
					<td>Adds an icon to the lef of the title.</td>
					<td>
						Any valid vui-icon name. <br>
						See list of <router-link to="/">vui-icons</router-link>
					</td>
				</tr>
				<tr>
					<th scope="row">title</th>
					<td>Sets the tilte text of the modal.</td>
					<td>Any valid string</td>
				</tr>
				<tr>
					<th scope="row">color</th>
					<td>Sets the background color of the modal's header.</td>
					<td>primary, secondary, success, danger, warning, info, light, dark</td>
				</tr>
				<tr>
					<th scope="row">position</th>
					<td>Sets the vertical position of the modal</td>
					<td>Any whole number</td>
				</tr>
				<tr>
					<th scope="row">topOffset</th>
					<td>
						Sets the offset distance from the top of the browser window as a percentag. <br>
						Only applies when you set the position to top.
					</td>
					<td>Any whole number</td>
				</tr>
				<tr>
					<th scope="row">width</th>
					<td>Sets the width in percentanges of the modal</td>
					<td>Any whole number</td>
				</tr>
				<tr>
					<th scope="row">headerTemplate</th>
					<td>Disables internal header and lets you use a custom template.</td>
					<td>n/a</td>
				</tr>
				<tr>
					<th scope="row">footerTemplate</th>
					<td>Disables internal footer and lets you use a custom template.</td>
					<td>n/a</td>
				</tr>
				<tr>
					<th scope="row">noFooter</th>
					<td>Disables internal footer and will render modal without one.</td>
					<td>n/a</td>
				</tr>
			</tbody>
		</table>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-modal icon="warning-sign" title="Modal Title">
	Modal body text goes here.
</vui-modal>
`
const ex1a = `
<vui-modal
	title="Modal Title"
	position="center" 
	v-show="showModal" 

	// Use this to hide the modal
	@close="showModal = !showModal"

	// Provide your own functionality for
	// what happens when 'Ok' button is clicked
	@ok="yourFunction"
>
	...
</vui-modal>
`
const ex2 = `
<vui-modal
	title="Modal Title"
	position="center" 
	v-show="showModal" 
	@close="showModal = !showModal"
	@ok="myFnc"
>
	<h5>Absolute Centered Modal</h5>
	<p>Modal body text goes here.</p>
</vui-modal>

--------------------------------------------

export default {
	data() {
		return {
			showModal: false
		}
	}
}
`
const ex3 = `
<vui-modal
	position="center" 
	v-show="showModal1" 
	@close="showModal1 = !showModal1"
	@ok="myFnc"
	headerTemplate
>
	<template slot="header">
		<div class="d-flex justify-content-center">
			<h4>Template Modal Title</h4>
		</div>
	</template>

	<h5>Vertically Centered Modal</h5>
	<p>Modal body content goes here.</p>
</vui-modal>
`
const ex4 = `
<vui-modal
	position="center" 
	v-show="showModal3" 
	@close="showModal3 = !showModal3"
	footerTemplate
>
	<template slot="footer">
		<vui-button @click="showModal3 = !showModal3">Cancel</vui-button>
		<vui-button color="primary" @click="myFunction">Ok</vui-button>
	</template>

	<h5>Vertically Centered Modal</h5>
	<p>Modal body content goes here.</p>
</vui-modal>
`
const ex5 = `
<vui-modal
	position="top" 
	v-show="showModal4" 
	@close="showModal4 = !showModal4"
>
	...
</vui-modal>
`
const ex6 = `
<vui-modal
	position="top"
	topOffset="15"
	v-show="showModal5" 
	@close="showModal5 = !showModal5"
>
	...
</vui-modal>
`
const ex7 = `
<vui-modal
	position="center"
	width="40"
	v-show="showModal6" 
	@close="showModal6 = !showModal6"
>
	...
</vui-modal>
`
const ex8 = `
<vui-modal
	position="center"
	color="primary"
	v-show="showModal7" 
	@close="showModal7 = !showModal7"
>
	...
</vui-modal>
`
const ex9 = `
<vui-modal
	position="center"
	v-show="showModal8" 
	@close="showModal8 = !showModal8"
	noFooter
>
	...
</vui-modal>
`

export default {
	name: 'Docs-Modal',
	data() {
		return {
			example1: ex1,
			example1a: ex1a,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8,
			example9: ex9,
			showModal1: false,
			showModal2: false,
			showModal3: false,
			showModal4: false,
			showModal5: false,
			showModal6: false,
			showModal7: false,
			showModal8: false
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Overview', link: 'overview' },
			{ label: 'Example', link: 'example' },
			{ label: 'Default Events', link: 'events' },
			{ label: 'Toggle Visibility', link: 'toggle_visibility' },
			{ label: 'Header Template', link: 'header_template' },
			{ label: 'Footer Template', link: 'footer_template' },
			{ label: 'Vertical Position', link: 'vertical_position' },
			{ label: 'Sizing', link: 'sizing' },
			{ label: 'Colors', link: 'colors' },
			{ label: 'Disable Footer', link: 'footer' },
			{ label: 'Modal Props', link: 'props' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		},
		myFunction() {
			return false
		}
	}
}
</script>


