<template>
	<div class="container">
		<h1 class="display-4">Download and Setup</h1>
		<p class="lead">
			Documentation for how to download and setup Vuiin your project.
		</p>

		<br>

		<h4 id="download">Download the latest version of Vui</h4>
		<p>
			The uncompressed Vui package is 434 KB. <br>
			<a href="./../../../static/vui-1.0.0.zip" class="btn btn-vui btn-lg w-25 mt-3 mr-3">Download Vui 1.0.0</a>
		</p>

		<br>

		<h3 id="install">Installing Vui Required Files</h3>
		<p>
			Looking to quickly add Vui to your project? Use the following CSS & JS files.
		</p>

		<br>

		<h4 id="css">CSS</h4>
		<p>
			Copy-paste the stylesheet <vui-code>&lt;link&gt;</vui-code> into your <vui-code>&lt;head&gt;</vui-code> 
			before all other stylesheets to Bootstrap's CSS.
		</p>
		<vui-code-panel>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br>

		<h4 id="js">JS</h4>
		<p>
			Several of the Vui components require the use of JavaScript helper libraries to function. 
			Place the following <vui-code>&lt;script&gt;</vui-code>s near the end of your pages, right 
			before the closing <vui-code>&lt;body&gt;</vui-code> tag.
		</p><vui-code-panel>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<p>
			* Webpack's eslinter prevented the closing <code>&lt;/script&gt;</code> tags from rendering in the code panel. 
			Make sure if you copy the code above that you add the closing tags.
		</p>

		<br><br>

		<h3 id="add">Adding Vui to Your Project</h3>
		<p>
			After you unzip the <code>vui-1.0.0.zip</code> file, place it in the root of your projects <code>src</code> 
			directory.
		</p>
		<img src="../../../static/images/vui-src-dir.png">
		<br><br>
		<p>
			Now that you have the Vui folder added to your project, let's take a look at whats inside:
		</p>
		<img src="../../../static/images/vui-dir-contents.png" style="width:720px">
		<br><br>
		<ul>
			<li>
				<code>components</code>: contains all of Vui's main components.
			</li>
			<li>
				<code>directives</code>: contains all of Vui's utility directives.
			</li>
			<li>
				<code>images</code>: misc graphics required for components.
			</li>
			<li>
				<code>widgets</code>: are items that combine several compoonets. i.e. Login Panel
			</li>
			<li>
				<code>vui_filters.js</code>: custom utiltiy filters.
			</li>
			<li>
				<code>vui.css</code>: various global component styles.
			</li>
			<li>
				<code>vui.js</code>: the main module that imports an registers everything in a central location.
			</li>
		</ul>

		<br><br>

		<h3 id="register">Registering Vui with Your Project</h3>
		<p>
			In order for Vui to work with your project, you have to tell it that it exists.
			Head on over to your <code>main.js</code> file, and <code>import</code> <code>vui.js</code>.
			This will globally register all of Vui's components, so they are all accessible from
			anywher in your project without the need to import them into your vue files.
		</p>
		<vui-code-panel type="javascript">
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>
		<br>
		<p>
			That's it, you are all set and ready to start using Vui's components in your project.
		</p>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<!-- Required for Vui -->
<link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.1.1/css/bootstrap.min.css" integrity="sha384-WskhaSGFgHYWDcbwN70/dfYBj47jz9qbsMId/iRN3ewGhXQFZCSftd1LZCfmhktB" crossorigin="anonymous">
<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/3.2.1/css/font-awesome.css" />

<!-- Required for COMPONENT: CodePanel -->
<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/prism/1.5.0/themes/prism.min.css">
`
const ex2 = `
<!-- Required for COMPONENT: CodePanel -->
<script src="https://cdnjs.cloudflare.com/ajax/libs/clipboard.js/2.0.0/clipboard.min.js">
<script src="https://cdnjs.cloudflare.com/ajax/libs/prism/1.5.0/prism.min.js">

<!-- Required for UTILITY: Draggable -->
<script src="https://cdnjs.cloudflare.com/ajax/libs/gsap/2.0.1/TweenMax.min.js">
<script src="https://cdnjs.cloudflare.com/ajax/libs/gsap/2.0.1/utils/Draggable.min.js">
`
const ex3 = `
import './vui/vui.js'
`

export default {
	name: 'Docs-Download-Setup',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Download Vui', link: 'download' },
			{ label: 'Installing Files', link: 'install' },
			{ label: '- CSS', link: 'css' },
			{ label: '- JS', link: 'js' },
			{ label: 'Add Vui to Project', link: 'add' },
			{ label: 'Globally Register Vui', link: 'register' },
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>

<style scoped>
.btn-vui {
	background-color: white;
	color: #42b883;
	border: 1px solid #42b883;
	font-size: 18px;
}
.btn-vui:hover {
	background-color: #42b883;
	color: white;
}
</style>



