<template>
	<div class="container">
		<h1 class="display-4">Dropdowns</h1>
		<p class="lead">
			Dropdowns are toggleable, contextual overlays for displaying 
			lists of links and actions in a dropdown menu format.
		</p>

		<h3 id="example">Example</h3>
		<p>
			Dropdown buttons are toggleable, contextual overlays for displaying 
			lists of links and more. Theyre toggled by clicking, not by hovering.
		</p>

		<h4 id="single_btn">Single Button Dropdown</h4>
		<p>
			These dropdowns are intended for use with the Vue-Router.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dropdown-button text="Dropdown">
					<vui-dropdown-item to="/docs/components/dropdowns">Item 1</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 2</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 3</vui-dropdown-item>
				</vui-dropdown-button>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">text</th>
					<td>Sets the label on the button.</td>
					<td>plain text</td>
				</tr>
				<tr>
					<th scope="row">to</th>
					<td>Defines a route to link one component to another.</td>
					<td>plain text</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h4 id="split_btn">Split Dropdown</h4>
		<p>
			Split button dropdowns are virtually the same markup as single button dropdowns.
			The left button provides standard click event support, while the right hand side 
			is the dropdown menu toggle button.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dropdown-button-split text="Load Profile" @click="test">
					<vui-dropdown-item @click="test">Call Function</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Goto Route</vui-dropdown-item>
					<vui-dropdown-item href="#">Download File</vui-dropdown-item>
				</vui-dropdown-button-split>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Event</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">@click="testFunc"</th>
					<td>Applies click event to the button</td>
					<td>function name</td>
				</tr>
				<tr>
					<th scope="row">@click="anotherFunc"</th>
					<td>Applies click event to the dropdown-item.</td>
					<td>function name</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="dropdown_item">Menu Items</h3>
		<p>
			Dropdown Menu Items can be either a <code>&lt;router-link&gt;</code> or standard 
			<code>&lt;a&gt;</code> link type. Which type is determined by the props you add. 
			So they can be mixed depending on your needs.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dropdown-link text="Dropdown">
					<vui-dropdown-item href="#">Download Vui</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Page 1</vui-dropdown-item>
				</vui-dropdown-link>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>
		
		<br><br>

		<h4 id="item_header">Menu Header</h4>
		<p>
			Add a header to label sections of actions in any dropdown menu.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dropdown-button text="Dropdown">
					<vui-dropdown-item-header>Dropdown Header</vui-dropdown-item-header>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 1</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 2</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 3</vui-dropdown-item>
				</vui-dropdown-button>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="item_text">Menu Text</h4>
		<p>
			Add a header to label sections of actions in any dropdown menu.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dropdown-button text="Dropdown">
					<vui-dropdown-item-text>Dropdown Text</vui-dropdown-item-text>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 1</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 2</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 3</vui-dropdown-item>
				</vui-dropdown-button>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>

		<br><br>

		<h4 id="item_divider">Menu Divider</h4>
		<p>
			Separate groups of related menu items with a divider.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-dropdown-button text="Dropdown">
					<vui-dropdown-item-text>Dropdown Text</vui-dropdown-item-text>
					<vui-dropdown-divider />
					<vui-dropdown-item to="/docs/components/dropdowns">Item 1</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 2</vui-dropdown-item>
					<vui-dropdown-item to="/docs/components/dropdowns">Item 3</vui-dropdown-item>
				</vui-dropdown-button>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="coloring">Coloring</h3>
		<p>
			You can use any Bootstrap default color. By default a <code>vui-dropdown-link</code> 
			will be displayed as an outline button. You can also use a <code>vui-dropdown-button</code>
			with the <code>outline</code> prop.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-row>
					<vui-dropdown-link text="Primary" color="primary" class="ml-3 mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-link>
					<vui-dropdown-link text="Secondary" color="secondary" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-link>
					<vui-dropdown-link text="Success" color="success" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-link>
					<vui-dropdown-link text="Danger" color="danger" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-link>
					<vui-dropdown-link text="Warning" color="warning" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-link>
					<vui-dropdown-link text="Info" color="info" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-link>

				</vui-row>
				<vui-row class="mt-3">
					<vui-dropdown-button text="Primary" color="primary" class="ml-3 mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>
					<vui-dropdown-button text="Secondary" color="secondary" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>
					<vui-dropdown-button text="Success" color="success" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>
					<vui-dropdown-button text="Danger" color="danger" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>
					<vui-dropdown-button text="Warning" color="warning" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>
					<vui-dropdown-button text="Info" color="info" class="mr-1">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>
				</vui-row>
			</template>
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">color</th>
					<td>Applies the background color.</td>
					<td>primary, secondary, success, danger, warning, info, light, dark</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="sizing">Sizing</h3>
		<p>
			Button dropdowns work with buttons of all sizes, 
			including default and split dropdown buttons.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-row>
					<vui-dropdown-button text="Large Button" class="ml-3 mr-1" size="lg">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>

					<vui-dropdown-button-split text="Large Split Button" class="mr-1" size="lg">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button-split>
				</vui-row>
				<br>
				<vui-row>
					<vui-dropdown-button text="Small Button" class="ml-3 mr-1" size="sm">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button>

					<vui-dropdown-button-split text="Small Split Button" class="mr-1" size="sm">
						<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
						<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
					</vui-dropdown-button-split>
				</vui-row>
			</template>
			<template slot="code">{{ example8 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">size</th>
					<td>Changes the size of the button.</td>
					<td>sm, lg</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="navs">Use with Navs</h3>
		<p>
			Add a <code>vui-dropdown-button</code> to a <code>vui-nav</code>, then add the <code>navLink</code> prop
			to match the link color.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav>
					<vui-nav-item active>Active</vui-nav-item>
					<vui-dropdown-button text="Dropdown" color="light" navLink>
						<vui-dropdown-item to="/">Item 1</vui-dropdown-item>
						<vui-dropdown-item to="/">Item 2</vui-dropdown-item>
						<vui-dropdown-item to="/">Item 3</vui-dropdown-item>
					</vui-dropdown-button>
					<vui-nav-item>Link</vui-nav-item>
					<vui-nav-item disabled>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example9 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-dropdown-button text="Dropdown">
	<vui-dropdown-item to="/">Item 1</vui-dropdown-item>
	<vui-dropdown-item to="/">Item 2</vui-dropdown-item>
	<vui-dropdown-item to="/">Item 3</vui-dropdown-item>
</vui-dropdown-button>
`
const ex2 = `
<vui-dropdown-button-split text="Load Profile" @click="testFnc">
	<vui-dropdown-item @click="anotherFnc">Call Function</vui-dropdown-item>
	<vui-dropdown-item to="...">Goto Route</vui-dropdown-item>
	<vui-dropdown-item href="...">Download File</vui-dropdown-item>
</vui-dropdown-button-split>
`

const ex3 = `
<vui-dropdown-item href="/downloads/vui.zip">Download Vui</vui-dropdown-item>
<vui-dropdown-item to="/page_1">Page 1</vui-dropdown-item>
`

const ex4 = `
<vui-dropdown-button text="Dropdown">
	<vui-dropdown-item-header>Dropdown Header</vui-dropdown-item-header>
	<vui-dropdown-item to="...">Item 1</vui-dropdown-item>
	<vui-dropdown-item to="...">Item 2</vui-dropdown-item>
	<vui-dropdown-item to="...">Item 3</vui-dropdown-item>
</vui-dropdown-button>
`

const ex5 = `
<vui-dropdown-button text="Dropdown">
	<vui-dropdown-item-text>Dropdown Text</vui-dropdown-item-text>
	<vui-dropdown-item to="/docs/components/dropdowns">Item 1</vui-dropdown-item>
	<vui-dropdown-item to="/docs/components/dropdowns">Item 2</vui-dropdown-item>
	<vui-dropdown-item to="/docs/components/dropdowns">Item 3</vui-dropdown-item>
</vui-dropdown-button>
`

const ex6 = `
<vui-dropdown-button text="Dropdown">
	<vui-dropdown-item-text>Dropdown Text</vui-dropdown-item-text>
	<vui-dropdown-divider />
	<vui-dropdown-item to="/docs/components/dropdowns">Item 1</vui-dropdown-item>
	<vui-dropdown-item to="/docs/components/dropdowns">Item 2</vui-dropdown-item>
	<vui-dropdown-item to="/docs/components/dropdowns">Item 3</vui-dropdown-item>
</vui-dropdown-button>
`

const ex7 = `
// Dropdown Button w/ outline
<vui-dropdown-button text="Primary" color="primary" class="ml-3 mr-1" outline>
	<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
</vui-dropdown-button>

// Dropdown link (outlined by default)
<vui-dropdown-link text="Primary" color="primary" class="ml-3 mr-1">
	<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
</vui-dropdown-link>

// Dropdown Button Solid Color
<vui-dropdown-button text="Primary" color="primary" class="ml-3 mr-1">
	<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
</vui-dropdown-button>
`

const ex8 = `
<vui-dropdown-button text="Large Button" class="ml-3 mr-1" size="lg">
	<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
</vui-dropdown-button>

<vui-dropdown-button-split text="Large Split Button" class="mr-1" size="lg">
	<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
</vui-dropdown-button-split>

<vui-dropdown-button text="Small Button" class="ml-3 mr-1" size="sm">
	<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
</vui-dropdown-button>

<vui-dropdown-button-split text="Small Split Button" class="mr-1" size="sm">
	<vui-dropdown-item href="#">Link 1</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 2</vui-dropdown-item>
	<vui-dropdown-item href="#">Link 3</vui-dropdown-item>
</vui-dropdown-button-split>
`
const ex9 = `
<vui-nav>
	<vui-nav-item active>Active</vui-nav-item>
	<vui-dropdown-button text="Dropdown" color="light" navLink>
		<vui-dropdown-item to="/">Item 1</vui-dropdown-item>
		<vui-dropdown-item to="/">Item 2</vui-dropdown-item>
		<vui-dropdown-item to="/">Item 3</vui-dropdown-item>
	</vui-dropdown-button>
	<vui-nav-item>Link</vui-nav-item>
	<vui-nav-item disabled>Disabled</vui-nav-item>
</vui-nav>
`

export default {
	name: 'Docs-Badge',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8,
			example9: ex9
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Examples', link: 'example' },
			{ label: '- Single Button', link: 'single_btn' },
			{ label: '- Split Button', link: 'split_btn' },
			{ label: 'Menu Items', link: 'dropdown_item' },
			{ label: '- Header', link: 'item_header' },
			{ label: '- Text', link: 'item_text' },
			{ label: '- Divider', link: 'item_divider' },
			{ label: 'Coloring', link: 'coloring' },
			{ label: 'Sizing', link: 'sizing' },
			{ label: 'Use with Navs', link: 'navs' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		},
		test() {
			console.log('Test fnc called')
		}
	}
}
</script>


