<template>
	<div class="container">
		<h1 class="display-4">Borders</h1>
		<p class="lead">
			Use border utilitiy directives to quickly style the border and border-radius of an element. 
			Great for images, buttons, or any other element.
		</p>

		<br>
		<vui-callout type="warning">
			The examples below use the <vui-code>vui-image-placeholder</vui-code> component. The component's 
			props were omiited in the code examples to simplify the code. Refer to the components 
			<router-link to="/docs/components/image_placeholder">docs</router-link> for the required props.
		</vui-callout>
		<br>

		<h3 id="border">Border</h3>
		<p>
			Use border utilities to add or remove an elements borders. Choose from all borders or one at a time.
		</p>

		<br>

		<h4 id="base_nav">Additive</h4>
		<vui-code-panel>
			<template slot="example">
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:top />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:left />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:right />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:bottom />
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br>

		<h4 id="subtractive">Subtractive</h4>
		<vui-code-panel>
			<template slot="example">
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border.0 />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:top.0 />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:left.0 />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:right.0 />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:bottom.0 />
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="border_color">Border Color</h3>
		<p>
			Change the border color by passing in the color values built on Bootstrap's theme colors.
		</p>
		<vui-callout type="warning">
			You must wrap the color name in single quotes if your not assigning a data value or computed property.
		</vui-callout>
		<vui-code-panel>
			<template slot="example">
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'secondary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'success'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'danger'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'warning'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'info'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'light'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border="'dark'" />

				<hr>

				<h6>Combine with additive or subtractive modifiers</h6>
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:top="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:left="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:right="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:bottom="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:top.0="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:left.0="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:right.0="'primary'" />
				<vui-img-placeholder color="light" size="75x75" class="mr-3" v-border:bottom.0="'primary'" />
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="border_radius">Border Radius</h3>
		<p>
			Add rounded modifiers to easily round its corners.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-img-placeholder color="secondary" size="75x75" class="mr-3" v-border.rounded />
				<vui-img-placeholder color="secondary" size="75x75" class="mr-3" v-border.rounded-top />
				<vui-img-placeholder color="secondary" size="75x75" class="mr-3" v-border.rounded-right />
				<vui-img-placeholder color="secondary" size="75x75" class="mr-3" v-border.rounded-bottom />
				<vui-img-placeholder color="secondary" size="75x75" class="mr-3" v-border.rounded-left />
				<vui-img-placeholder color="secondary" size="75x75" class="mr-3" v-border.rounded-circle />
				<vui-img-placeholder color="secondary" size="75x75" class="mr-3" v-border.rounded-0 />
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-img-placeholder v-border />
<vui-img-placeholder v-border:top />
<vui-img-placeholder v-border:left />
<vui-img-placeholder v-border:right />
<vui-img-placeholder v-border:bottom />
`
const ex2 = `
<vui-img-placeholder v-border.0 />
<vui-img-placeholder v-border:top.0 />
<vui-img-placeholder v-border:left.0 />
<vui-img-placeholder v-border:right.0 />
<vui-img-placeholder v-border:bottom.0 />
`
const ex3 = `
<vui-img-placeholder v-border="'primary'" />
<vui-img-placeholder v-border="'secondary'" />
<vui-img-placeholder v-border="'success'" />
<vui-img-placeholder v-border="'danger'" />
<vui-img-placeholder v-border="'warning'" />
<vui-img-placeholder v-border="'info'" />
<vui-img-placeholder v-border="'light'" />
<vui-img-placeholder v-border="'dark'" />

<h6>Combine with additive or subtractive modifiers</h6>
<vui-img-placeholder v-border:top="'primary'" />
<vui-img-placeholder v-border:left="'primary'" />
<vui-img-placeholder v-border:right="'primary'" />
<vui-img-placeholder v-border:bottom="'primary'" />
<vui-img-placeholder v-border:top.0="'primary'" />
<vui-img-placeholder v-border:left.0="'primary'" />
<vui-img-placeholder v-border:right.0="'primary'" />
<vui-img-placeholder v-border:bottom.0="'primary'" />
`
const ex4 = `
<vui-img-placeholder v-border.rounded />
<vui-img-placeholder v-border.rounded-top />
<vui-img-placeholder v-border.rounded-right />
<vui-img-placeholder v-border.rounded-bottom />
<vui-img-placeholder v-border.rounded-left />
<vui-img-placeholder v-border.rounded-circle />
<vui-img-placeholder v-border.rounded-0 />
`

export default {
	name: 'Docs-Borders',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Border', link: 'border' },
			{ label: '- Additive', link: 'additive' },
			{ label: '- Subtractive', link: 'subtractive' },
			{ label: 'Border Color', link: 'border_color' },
			{ label: 'Border Radius', link: 'border_radius' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


