<template>
	<div class="container">
		<h1 class="display-4">Navs</h1>
		<p class="lead">
			Documentation and examples for how to use Vuis included navigation components.
		</p>

		<br>

		<h3 id="base_nav">Base Nav</h3>
		<p>
			Navigation available in Vui share general markup and styles, from the base <code>vui-nav</code> to the active 
			and disabled props. Swap modifier props to switch between each style.
		</p>
		<p>
			The base <code>vui-nav</code> component is built with flexbox and provides a strong foundation for building all types 
			of navigation components. It includes some style overrides (for working with lists), some link padding 
			for larger hit areas, and basic disabled styling.
		</p>
		<p>
			Vui was designed to be used with SPA's (Single Page Applications), so the <code>vui-nav-item</code> uses a 
			<code>router-link</code> and not a standard anchor tag.
		</p>
		<vui-callout type="info"> 
			The base <span class="pl-1 pr-1"><code>vui-nav-item</code></span> component includes active and disabled states.
		</vui-callout>
		<vui-code-panel>
			<template slot="example">
				<vui-nav>
					<vui-nav-item to="/docs/components/navs" active>Active</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item disabled>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="appearance">Appearance</h3>
		<p>
			Change the appearance of <code>vui-nav</code> component with props and utilities. 
			Mix and match as needed, or build your own.
		</p>

		<br>

		<h3 id="horizontal_alignment">Horizontal Alignment</h3>
		<p>
			Change the horizontal alignment of your nav with flexbox utilities. By default, navs are left-aligned, 
			but you can easily change them to center or right aligned.
		</p>
		<p>
			Centered with <code>.justify-content-center</code>
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav class="justify-content-center">
					<vui-nav-item to="/docs/components/navs" active>Active</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item disabled>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<br>
		<p>
			Right-aligned with <code>.justify-content-end</code>
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav class="justify-content-end">
					<vui-nav-item to="/docs/components/navs" active>Active</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item disabled>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="vertical">Vertical</h3>
		<p>
			Stack your navigation by changing the flex item direction with the <code>.flex-column</code> utility. 
			Need to stack them on some viewports but not others? Use the responsive versions (e.g., <code>.flex-sm-column</code>).
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav class="flex-column">
					<vui-nav-item to="/docs/components/navs" active>Active</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item to="/docs/components/navs">Link</vui-nav-item>
					<vui-nav-item disabled>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="tabs">Tabs</h3>
		<p>
			Takes the basic nav from above and adds the <code>tabs</code> and <code>tab</code> props to generate a tabbed interface.
		</p>
		<vui-callout type="info">
			It also converts the <span class="pl-1 pr-1"><code>vui-nav-item</code></span> from a 
			<span class="pl-1 pr-1"><code>router-link</code></span> to an anchor tag.
		</vui-callout>
		<vui-code-panel>
			<template slot="example">
				<vui-nav tabs>
					<vui-nav-item active tab>Active</vui-nav-item>
					<vui-nav-item tab>Link</vui-nav-item>
					<vui-nav-item tab>Link</vui-nav-item>
					<vui-nav-item disabled tab>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="pills">Pills</h3>
		<p>
			Take that same component makrup, but use <code>pills</code> and <code>pill</code> props instead:
		</p>
		<vui-callout type="warning">
			Pills convert the <span class="pl-1 pr-1"><code>vui-nav-item</code></span> back to a 
			<span class="pl-1 pr-1"><code>router-link</code></span>.
		</vui-callout>
		<vui-code-panel>
			<template slot="example">
				<vui-nav pills>
					<vui-nav-item to="/docs/components/navs" active pill>Active</vui-nav-item>
					<vui-nav-item to="/docs/components/navs" pill>Link</vui-nav-item>
					<vui-nav-item to="/docs/components/navs" pill>Link</vui-nav-item>
					<vui-nav-item disabled pill>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="fill">Fill and Justify</h3>
		<p>
			Force your <code>vui-nav</code> contents to extend the full available width one of two modifier props. 
			To proportionately fill all available space with your <code>vui-nav-item</code>, use the <code>fill</code>
			prop. Notice that all horizontal space is occupied, but not every <code>vui-nav-item</code> has the same width.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav tabs fill>
					<vui-nav-item active tab>Active</vui-nav-item>
					<vui-nav-item tab>Longer Link</vui-nav-item>
					<vui-nav-item tab>Link</vui-nav-item>
					<vui-nav-item disabled tab>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>
		<br>
		<p>
			For equal-width elements, use <code>justified</code> prop. All horizontal space will be occupied by <code>vui-nav-item</code>'s, 
			but unlike the <code>fill</code> prop above, every nav <code>vui-nav-item</code> be the same width.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav tabs justified>
					<vui-nav-item active tab>Active</vui-nav-item>
					<vui-nav-item tab>Longer Link</vui-nav-item>
					<vui-nav-item tab>Link</vui-nav-item>
					<vui-nav-item disabled tab>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example8 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="dropdowns">Using Dropdowns</h3>
		<p>
			Add a <code>vui-dropdown-button</code> with the <code>navLink</code> prop.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-nav>
					<vui-nav-item active>Active</vui-nav-item>
					<vui-dropdown-button text="Dropdown" color="light" navLink>
						<vui-dropdown-item to="/">Item 1</vui-dropdown-item>
						<vui-dropdown-item to="/">Item 2</vui-dropdown-item>
						<vui-dropdown-item to="/">Item 3</vui-dropdown-item>
					</vui-dropdown-button>
					<vui-nav-item>Link</vui-nav-item>
					<vui-nav-item disabled>Disabled</vui-nav-item>
				</vui-nav>
			</template>
			<template slot="code">{{ example9 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-nav>
	<vui-nav-item to="..." active>Active</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="..." disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex2 = `
<vui-nav class="justify-content-center">
	<vui-nav-item to="..." active>Active</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="..." disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex3 = `
<vui-nav class="justify-content-end">
	<vui-nav-item to="..." active>Active</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="..." disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex4 = `
<vui-nav class="flex-column">
	<vui-nav-item to="..." active>Active</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="...">Link</vui-nav-item>
	<vui-nav-item to="..." disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex5 = `
<vui-nav tabs>
	<vui-nav-item @click="tab1" tab active>Active</vui-nav-item>
	<vui-nav-item @click="tab2" tab>Link</vui-nav-item>
	<vui-nav-item @click="tab3" tab>Link</vui-nav-item>
	<vui-nav-item @click="tab4" tab disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex6 = `
<vui-nav pills>
	<vui-nav-item to="..." pill active>Active</vui-nav-item>
	<vui-nav-item to="..." pill>Link</vui-nav-item>
	<vui-nav-item to="..." pill>Link</vui-nav-item>
	<vui-nav-item to="..." pill disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex7 = `
<vui-nav tabs fill>
	<vui-nav-item @click="tab1" tab active>Active</vui-nav-item>
	<vui-nav-item @click="tab2" tab>Longer Link</vui-nav-item>
	<vui-nav-item @click="tab3" tab>Link</vui-nav-item>
	<vui-nav-item @click="tab4" tab disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex8 = `
<vui-nav tabs justified>
	<vui-nav-item @click="tab1" tab active>Active</vui-nav-item>
	<vui-nav-item @click="tab2" tab>Longer Link</vui-nav-item>
	<vui-nav-item @click="tab3" tab>Link</vui-nav-item>
	<vui-nav-item @click="tab4" tab disabled>Disabled</vui-nav-item>
</vui-nav>
`
const ex9 = `
<vui-nav>
	<vui-nav-item active>Active</vui-nav-item>
	<vui-dropdown-button text="Dropdown" color="light" navLink>
		<vui-dropdown-item to="/">Item 1</vui-dropdown-item>
		<vui-dropdown-item to="/">Item 2</vui-dropdown-item>
		<vui-dropdown-item to="/">Item 3</vui-dropdown-item>
	</vui-dropdown-button>
	<vui-nav-item>Link</vui-nav-item>
	<vui-nav-item disabled>Disabled</vui-nav-item>
</vui-nav>
`

export default {
	name: 'Docs-Navs',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8,
			example9: ex9
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Base Nav', link: 'base_nav' },
			{ label: 'Appearance', link: 'appearance' },
			{ label: '- Horizontal Alignment', link: 'horizontal_alignment' },
			{ label: '- Vertical', link: 'vertical' },
			{ label: '- Tabs', link: 'tabs' },
			{ label: '- Pills', link: 'pills' },
			{ label: '- Fill & Justify', link: 'fill' },
			{ label: 'Using Dropdowns', link: 'dropdowns' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>

<style scoped>
.callout.info {
	padding: 20px;
	border-left: 5px solid #17a2b8;
	border-top: 1px solid #ccc;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
	margin-top: 15px;
	margin-bottom: 15px;
	border-radius: 5px;
}
.callout.warning {
	padding: 20px;
	border-left: 5px solid #ffc107;
	border-top: 1px solid #ccc;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
	margin-top: 15px;
	margin-bottom: 15px;
	border-radius: 5px;
}
</style>



