<template>
	<span class="pl-1 pr-1" v-if="!isTag">
		<code>
			<slot></slot>
		</code>
	</span>
	<span class="pl-1 pr-1" v-else-if="isTag">
		<code>
			&lt;<slot></slot>&gt;
		</code>
	</span>
</template>

<script>
export default {
	name: 'vui-code',
	props: {
		'tag': { type: Boolean, default: false }
	},
	computed: {
		isTag() {
			return !!this.tag
		}
	}
}
</script>


