<template>
	<div class="container">
		<h1 class="display-4">Float</h1>
		<p class="lead">
			Toggle floats on any element, across any breakpoint, using the responsive float utility directive.
		</p>

		<br>

		<h3 id="overview">Overview</h3>
		<p>
			This utility directive floats an element to the left or right, or disables floating, based on the current 
			viewport size using the CSS float property. !important is included to avoid specificity issues. 
			These use the same viewport breakpoints as the Bootstrap grid system. Please be aware that the float 
			utility directive has no affect on flex items.
		</p>
		<vui-code-panel>
			<template slot="example">
				<div v-float:left>Float left on all viewport sizes</div><br>
				<div v-float:right>Float right on all viewport sizes</div><br>
				<div v-float:none>Don't float on all viewport sizes</div>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<div v-float:left>Float left on all viewport sizes</div>
<div v-float:right>Float right on all viewport sizes</div>
<div v-float:none>Don't float on all viewport sizes</div>
`
const ex2 = `
<div v-float:left.sm>Float left on viewports sized SM (small) or wider</div>
<div v-float:left.md>Float left on viewports sized MD (medium) or wider</div>
<div v-float:left.lg>Float left on viewports sized LG (large) or wider</div>
<div v-float:left.xl>Float left on viewports sized XL (extra-large) or wider</div>
`

export default {
	name: 'Docs-Float',
	data() {
		return {
			example1: ex1,
			example2: ex2
		}
	},
	mounted() {
		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


