		<h4 id="textarea">Textarea Input</h4>
		<p>
			The <code>&lt;vui-textarea&gt;</code> defines a multi-line text input control. 
			A text area can hold an unlimited number of characters, and the text renders in 
			a fixed-width font. The textarea cannot have an empty slot, so a default placeholder 
			is defined. You can change this text with the <code>placeholder=""</code> prop.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-form-group>
					<vui-form-label for="textarea1">Example Textarea</vui-form-label>
