		<h3 id="layout">Layouts</h3>
		<p>
			Since Bootstrap applies <code>display: block</code> and <code>width: 100%</code> 
			to almost the form controls, forms will by default stack vertically. Additional 
			props can be used to vary this layout on a per-form basis.
		</p>

		<br>

		<h4 id="form_groups">Form Groups</h4>
		<p>
			The <code>&lt;vui-form-group&gt;</code> component is the easiest way to add some 
			structure to forms. It provides a flexible class that encourages proper grouping 
			of labels, controls, optional help text, and form validation messaging. By default 
			it only applies margin-bottom, but it picks up additional styles in <code>inline</code> 
			prop as needed. Use it with <code>&lt;vui-fieldset&gt;</code>s, <code>&lt;divs&gt;</code>s, 
			or nearly any other element.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-form-group>
					<vui-form-label for="formGroupExampleInput">Example label</vui-form-label>
					<vui-input id="formGroupExampleInput" placeholder="Example input" />
				</vui-form-group>
				<vui-form-group>
					<vui-form-label for="formGroupExampleInput2">Another label</vui-form-label>
					<vui-input id="formGroupExampleInput2" placeholder="Another input" />
				</vui-form-group>
			</template>
			<template slot="code">{{ example9 }}</template>
		</vui-code-panel>

		<br>

		<h4 id="form_grid">Form Grid</h4>
		<p>
			More complex forms can be built using the grid compponent. Use these for form layouts 
			that require multiple columns, varied widths, and additional alignment options.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-form>
					<vui-form-row>
						<vui-form-col>
							<vui-input placeholder="First name" />
						</vui-form-col>
						<vui-form-col>
							<vui-input placeholder="Last name" />
						</vui-form-col>
					</vui-form-row>
				</vui-form>
			</template>
			<template slot="code">{{ example10 }}</template>
		</vui-code-panel>
		<br>
		<p>More complex layouts can also be created with the grid system.</p>
		<vui-code-panel>
			<template slot="example">
				<vui-form>
					<vui-form-row>
						<vui-form-group class="col-md-6">
							<vui-form-label for="input-Email-1">Email</vui-form-label>
							<vui-input type="email" id="input-Email-1" placeholder="Email" autocomplete="new-email" />
						</vui-form-group>
						<vui-form-group class="col-md-6">
							<vui-form-label for="input-Password-1">Password</vui-form-label>
