						</vui-form-group>
					</vui-form-row>

					<vui-form-group>
						<vui-form-label for="inputAddress-1">Address</vui-form-label>
							<vui-input type="text" id="inputAddress-1" placeholder="1234 Main St" />
					</vui-form-group>

					<vui-form-group>
						<vui-form-label for="inputAddress-2">Address 2</vui-form-label>
						<vui-input type="text" id="inputAddress-2" placeholder="Apartment, studio, or floor" />
					</vui-form-group>

					<vui-form-row>
						<vui-form-group class="col-md-6">
