		<h4 id="inline_form">Inline Form</h4>
		<p>
			Create horizontal forms with the grid by using the <code>&lt;vui-form&gt;</code> with the
			<code>inline</code> prop.
		</p>
		<p>
			At times, you maybe need to use margin or padding utilities to create that perfect alignment you need. 
			For example, Bootstrap removed the padding-top on the stacked radio inputs label to better align 
			the text baseline.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-form inline>
					<vui-input placeholder="Jane Doe" />
					<vui-input-group class="ml-sm-2">
						<vui-input-group-prepend text>@</vui-input-group-prepend>
						<vui-input placeholder="Username" />
					</vui-input-group>
					<vui-button class="ml-sm-2" color="primary">Submit</vui-button>
				</vui-form>
			</template>
			<template slot="code">{{ example12 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="help_text">Help Text</h3>
		<p>
			Block-level help text in forms can be created using <code>&lt;vui-form-help-text&gt;</code>.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-form>
					<vui-form-label for="inputPassword-13">Password</vui-form-label>
					<vui-input id="inputPassword-13" type="password" />
					<vui-form-help-text>
						Your password must be 8-20 characters long, contain letters and numbers.
					</vui-form-help-text>
				</vui-form>
			</template>
