<template>
	<div class="container">
		<h1 class="display-4">Button Group</h1>
		<p class="lead">
			Group a series of buttons together on a single 
			line with the button group.
		</p>

		<h3 id="example">Basic Example</h3>
		<p>Wrap a series of buttons with a <code>&lt;vui-button-group&gt;</code>.</p>

		<vui-code-panel>
			<template slot="example">
				<vui-button-group>
					<vui-button>Left</vui-button>
					<vui-button>Middle</vui-button>
					<vui-button>Right</vui-button>
				</vui-button-group>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		
		<br><br>

		<h3 id="colors">Colors</h3>
		<p>
			Instead of applying button colors to every button in a group, 
			just add the <code>color</code> prop. This will override the 
			default color of the button.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-button-group color="primary">
					<vui-button>Left</vui-button>
					<vui-button>Middle</vui-button>
					<vui-button>Right</vui-button>
				</vui-button-group>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">color</th>
					<td>Sets the color of the badge</td>
					<td>primary, secondary, success, danger, warning, info, light, dark</td>
				</tr>
			</tbody>
		</table>
		
		<br><br>

		<h3 id="button_toolbar">Button Toolbar</h3>
		<p>
			Combine sets of button groups into button toolbars for more complex 
			components. Use utility classes as needed to space out groups, 
			buttons, and more.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-button-group>
					<vui-button>1</vui-button>
					<vui-button>2</vui-button>
					<vui-button>3</vui-button>
					<vui-button>4</vui-button>
				</vui-button-group>
				<vui-button-group>
					<vui-button>5</vui-button>
					<vui-button>6</vui-button>
					<vui-button>7</vui-button>
				</vui-button-group>
				<vui-button-group>
					<vui-button>8</vui-button>
				</vui-button-group>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<br>
		<p>
			Feel free to mix icons, dropdown-buttons and more to 
			create an application toolbar.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-button-group>
					<vui-button color="dark" outline>
						<i class="fa icon-align-left"></i>
					</vui-button>
					<vui-button color="dark" outline>
						<i class="fa icon-align-center"></i>
					</vui-button>
					<vui-button color="dark" outline>
						<i class="fa icon-align-right"></i>
					</vui-button>
					<vui-button color="dark" outline>
						<i class="fa icon-align-justify"></i>
					</vui-button>
				</vui-button-group>
				<vui-button-group>
					<vui-button color="dark" outline>
						<i class="fa icon-copy"></i>
					</vui-button>
					<vui-button color="dark" outline>
						<i class="fa icon-cut"></i>
					</vui-button>
					<vui-button color="dark" outline>
						<i class="fa icon-paste"></i>
					</vui-button>
				</vui-button-group>
				<vui-button-group>
					<vui-button color="dark" outline>
						<i class="fa icon-save"></i>
					</vui-button>
				</vui-button-group>
				<vui-button-group color="dark">
					<vui-dropdown-button text="Fonts">
						<vui-dropdown-item>Arial</vui-dropdown-item>
						<vui-dropdown-item>Arial Rounded</vui-dropdown-item>
						<vui-dropdown-item>Arial Black</vui-dropdown-item>
						<vui-dropdown-item>Calibri</vui-dropdown-item>
					</vui-dropdown-button>
				</vui-button-group>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>
		
		<br><br>

		<h3 id="sizing">Sizing</h3>
		<p>
			Instead of applying button sizing classes to every button in a group, 
			just add the <code>size</code> prop.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-button-group size="lg">
					<vui-button>Left</vui-button>
					<vui-button>Middle</vui-button>
					<vui-button>Right</vui-button>
				</vui-button-group>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>
		<vui-code-panel>
			<template slot="example">
				<vui-button-group size="sm">
					<vui-button>Left</vui-button>
					<vui-button>Middle</vui-button>
					<vui-button>Right</vui-button>
				</vui-button-group>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">size</th>
					<td>Size of the button-group.</td>
					<td>sm, lg</td>
				</tr>
			</tbody>
		</table>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-button-group>
	<vui-button>Left</vui-button>
	<vui-button>Middle</vui-button>
	<vui-button>Right</vui-button>
</vui-button-group>
`

const ex2 = `
<vui-button-group>
	<vui-button>1</vui-button>
	<vui-button>2</vui-button>
	<vui-button>3</vui-button>
	<vui-button>4</vui-button>
</vui-button-group>
<vui-button-group>
	<vui-button>5</vui-button>
	<vui-button>6</vui-button>
	<vui-button>7</vui-button>
</vui-button-group>
<vui-button-group>
	<vui-button>8</vui-button>
</vui-button-group>
`

const ex3 = `
<vui-button-group>
	<vui-button color="dark" outline>
		<i class="fa icon-align-left"></i>
	</vui-button>
	<vui-button color="dark" outline>
		<i class="fa icon-align-center"></i>
	</vui-button>
	<vui-button color="dark" outline>
		<i class="fa icon-align-right"></i>
	</vui-button>
	<vui-button color="dark" outline>
		<i class="fa icon-align-justify"></i>
	</vui-button>
</vui-button-group>
<vui-button-group>
	<vui-button color="dark" outline>
		<i class="fa icon-copy"></i>
	</vui-button>
	<vui-button color="dark" outline>
		<i class="fa icon-cut"></i>
	</vui-button>
	<vui-button color="dark" outline>
		<i class="fa icon-paste"></i>
	</vui-button>
</vui-button-group>
<vui-button-group>
	<vui-button color="dark" outline>
		<i class="fa icon-save"></i>
	</vui-button>
</vui-button-group>
<vui-button-group>
	<vui-dropdown-button text="Fonts" color="dark" outline>
		<vui-dropdown-item>Arial</vui-dropdown-item>
		<vui-dropdown-item>Arial Rounded</vui-dropdown-item>
		<vui-dropdown-item>Arial Black</vui-dropdown-item>
		<vui-dropdown-item>Calibri</vui-dropdown-item>
	</vui-dropdown-button>
</vui-button-group>
`

const ex4 = `
<vui-button-group size="lg">
	<vui-button>Left</vui-button>
	<vui-button>Middle</vui-button>
	<vui-button>Right</vui-button>
</vui-button-group>
`

const ex5 = `
<vui-button-group size="sm">
	<vui-button>Left</vui-button>
	<vui-button>Middle</vui-button>
	<vui-button>Right</vui-button>
</vui-button-group>
`

const ex6 = `
<vui-button-group color="primary">
	<vui-button>Left</vui-button>
	<vui-button>Middle</vui-button>
	<vui-button>Right</vui-button>
</vui-button-group>
`

export default {
	name: 'Docs-Button-Group',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Basic Example', link: 'example' },
			{ label: 'Colors', link: 'colors' },
			{ label: 'Button Toolbar', link: 'button_toolbar' },
			{ label: 'Sizing', link: 'sizing' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500)
		}
	}
}
</script>


