<template>
	<div class="container">
		<h1 class="display-4">Jumbotron</h1>
		<p class="lead">
			Lightweight, flexible component for showcasing hero unit style content.
		</p>

		<br><br>
		<p>
			A lightweight, flexible component that can optionally extend the entire viewport 
			to showcase key messages in your application.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-jumbotron>
					<h1 class="display-4">Hello, world!</h1>
					<p class="lead">
						This is a simple hero unit, a simple jumbotron-style component for calling 
						extra attention to featured content or information.
					</p>
					<hr class="my-4">
					<p>
						It uses utility classes for typography and spacing to space content 
						out within the larger container.
					</p>
					<vui-button>Learn more</vui-button>
				</vui-jumbotron>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		
		<br>

		<p>
			To make the jumbotron full width, and without rounded corners, add the 
			<code>fluid</code> prop.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-jumbotron fluid>
					<h1 class="display-4">Fluid Jumbotron</h1>
					<p class="lead">
						This is a modified jumbotron that occupies the entire horizontal space of its parent.
					</p>
				</vui-jumbotron>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-jumbotron>
	<h1 class="display-4">Hello, world!</h1>
	<p class="lead">
		This is a simple hero unit, a simple jumbotron-style component for calling 
		extra attention to featured content or information.
	</p>
	<hr class="my-4">
	<p>
		It uses utility classes for typography and spacing to space content 
		out within the larger container.
	</p>
	<vui-button>Learn more</vui-button>
</vui-jumbotron>
`
const ex2 = `
<vui-jumbotron fluid>
	<h1 class="display-4">Fluid Jumbotron</h1>
	<p class="lead">
		This is a modified jumbotron that occupies the entire horizontal space of its parent.
	</p>
</vui-jumbotron>
`

export default {
	name: 'Docs-ImgPlaceholder',
	data() {
		return {
			example1: ex1,
			example2: ex2
		}
	},
	mounted() {
		this.$parent.tocLinks = []

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


