<template>
	<div class="container">
		<h1 class="display-4">Tooltips</h1>
		<p class="lead">
			Documentation and examples for adding custom tooltips using data-attributes for local title storage
			positon and colors.
		</p>

		<br>

		<h3 id="overview">Overview</h3>
		<p>
			Things to know when using the tooltip attributes:
		</p>
		<ul>
			<li>
				Tooltips are a pure CSS solution and have no javascript dependencies.
			</li>
			<li>
				Tooltips with zero-length titles will display an empty container.
			</li>
			<li>
				Tooltips can be applied to any element or component.
			</li>
			<li>
				Tooltips have four positions: <code>top, left, right, bottom</code>
			</li>
			<li>
				Tooltips can be colored: <code>primary, secondary, success, danger, warning, info, dark</code>
			</li>
		</ul>

		<br><br>

		<h3 id="examples">Examples</h3>
		<p>
			Hover over the the various elements below to see tooltips:
		</p>
		<vui-code-panel>
			<template slot="example">

				<h5>Using tooltips on a button.</h5>
				<button 
					class="btn btn-secondary" 
					tooltip="I'm a tooltip!" 
					tooltip-position="top"
					tooltip-color="dark"
				>
					Hover Me
				</button>

				<br>
				<hr>

				<h5>Using tooltips on links.</h5>
				<p>
					Lorem ipsum <a href="#" tooltip="I'm a tooltip on a link!" tooltip-position="top">TOOLTIP</a> 
					dolor sit amet, consectetur adipiscing elit. Donec ultrices vel diam at interdum. 
					Nam ac sem lobortis, <a href="#" tooltip="I'm another tooltip!" tooltip-position="top">TOOLTIP</a> 
					maximus augue eget, sollicitudin magna. Maecenas placerat varius odio, 
					at. Sed  <router-link to="/" tooltip="I'm on a router-link!" tooltip-position="top">TOOLTIP</router-link> 
					nibh maximus vestibulum ut eu leo. Aenean in mi et urna fringilla fermentum. In nisl lacus
				</p>

				<hr>
				
				<h5>Using them with span tags and icons</h5>
				<span tooltip="Facebook" tooltip-position="top">
					<vui-icon name="facebook" size="36px" />
				</span>
				<span tooltip="Twitter" tooltip-position="top" class="ml-3">
					<vui-icon name="twitter" size="36px" />
				</span>
				<span tooltip="GitHub" tooltip-position="top" class="ml-3">
					<vui-icon name="github" size="36px" />
				</span>

			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="positioning">Positioning</h3>
		<p>
			Four options are available for positioning: <code>top, left, right, bottom</code>.
			The default position is <code>top</code>. To set the position you add the
			<code>tooltip-position</code> attribute. Positioning is relative to the trigger element.
		</p>
		<vui-code-panel>
			<template slot="example">
				<div class="d-flex justify-content-center">
					<button 
						class="btn btn-secondary" 
						tooltip="I'm a tooltip on the left!" 
						tooltip-position="left"
					>
						Left
					</button>

					<button 
						class="btn btn-secondary ml-3" 
						tooltip="I'm a tooltip on the top!" 
					>
						Top
					</button>

					<button 
						class="btn btn-secondary ml-3" 
						tooltip="I'm a tooltip on the bottom!" 
						tooltip-position="bottom"
					>
						Bottom
					</button>

					<button 
						class="btn btn-secondary ml-3" 
						tooltip="I'm a tooltip on the right!" 
						tooltip-position="right"
					>
						Right
					</button>
				</div>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="colors">Colors</h3>
		<p>
			You can change the color of a tooltip by adding the <code>tooltip-color</code> attribute.
			You can apply the following colors:
		</p>
		<vui-code-panel>
			<template slot="example">
				<div class="d-flex justify-content-center">
					<button 
						class="btn btn-primary" 
						tooltip="I'm a primary tooltip!" 
						tooltip-color="primary"
					>
						Primary
					</button>

					<button 
						class="btn btn-secondary ml-3" 
						tooltip="I'm a secondary tooltip!"
						tooltip-color="secondary" 
					>
						Secondary
					</button>

					<button 
						class="btn btn-success ml-3" 
						tooltip="I'm a success tooltip!" 
						tooltip-color="success"
					>
						Success
					</button>

					<button 
						class="btn btn-danger ml-3" 
						tooltip="I'm a danger tooltip!" 
						tooltip-color="danger"
					>
						Danger
					</button>

					<button 
						class="btn btn-warning ml-3" 
						tooltip="I'm a warning tooltip!" 
						tooltip-color="warning"
					>
						Warning
					</button>

					<button 
						class="btn btn-info ml-3" 
						tooltip="I'm a info tooltip!" 
						tooltip-color="info"
					>
						Info
					</button>

					<button 
						class="btn btn-dark ml-3" 
						tooltip="I'm a dark tooltip!" 
						tooltip-color="dark"
					>
						Dark
					</button>
				</div>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<!-- On a button -->
<button tooltip="I'm a tooltip!" tooltip-position="top">Hover Me</button>

<!-- On links and router-links -->
<a href="#" tooltip="I'm a tooltip on a link!" tooltip-position="top">
<router-link to="/" tooltip="I'm on a router-link!" tooltip-position="top">TOOLTIP</router-link>

<!-- On span tags -->
<span tooltip="Facebook" tooltip-position="top">
	<vui-icon name="facebook" size="36px" />
</span>
<span tooltip="Twitter" tooltip-position="top" class="ml-3">
	<vui-icon name="twitter" size="36px" />
</span>
<span tooltip="GitHub" tooltip-position="top" class="ml-3">
	<vui-icon name="github" size="36px" />
</span>
`
const ex2 = `
<button tooltip="I'm a tooltip on the left!" tooltip-position="left">Left</button>

<button tooltip="I'm a tooltip on the top!">Top</button>

<button tooltip="I'm a tooltip on the bottom!" tooltip-position="bottom">Bottom</button>

<button tooltip="I'm a tooltip on the right!" tooltip-position="right">Right</button>
`
const ex3 = `
<button tooltip="I'm a primary tooltip!" tooltip-color="primary">Primary</button>

<button tooltip="I'm a secondary tooltip!" tooltip-color="secondary">Secondary</button>

<button tooltip="I'm a success tooltip!" tooltip-color="success">Success</button>

<button tooltip="I'm a danger tooltip!" tooltip-color="danger">Danger</button>

<button tooltip="I'm a warning tooltip!" tooltip-color="warning">Warning</button>

<button tooltip="I'm a info tooltip!" tooltip-color="info">Info</button>

<button tooltip="I'm a dark tooltip!" tooltip-color="dark">Dark</button>
`
const ex4 = `

`
const ex5 = `

`
const ex6 = `

`
const ex7 = `

`
const ex8 = `

`
const ex9 = `

`

export default {
	name: 'Docs-Tootips',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8,
			example9: ex9
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Overview', link: 'overview' },
			{ label: 'Examples', link: 'examples' },
			{ label: 'Positioning', link: 'positioning' },
			{ label: 'Colors', link: 'colors' }
			
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


