<template>
	<div class="container">
		<h1 class="display-4">Progress Bars</h1>
		<p class="lead">
			Documentation and examples for using Vui's custom progress bars featuring 
			support for stacked bars, animated backgrounds, and text labels.
		</p>

		<br>

		<h3 id="overview">Overview</h3>
		<p>
			The Progress Bar component is made of two components a handful of props.
		</p>
		<ul>
			<li>
				<code>vui-progress</code> is a wrapper to indicate the max value of the progress bar.
			</li>
			<li>
				<code>vui-progress-bar</code> indicates the progress so far.
			</li>
			<li>
				<code>vui-progress-bar</code> has several props for setting: 
				<code>label, width, colors, stripes and animation</code>.
			</li>
		</ul>
		<p>
			Put that all together, and you have the following examples:
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-progress>
					<vui-progress-bar width="0"></vui-progress-bar>
				</vui-progress>

				<vui-progress>
					<vui-progress-bar width="25"></vui-progress-bar>
				</vui-progress>

				<vui-progress>
					<vui-progress-bar width="50"></vui-progress-bar>
				</vui-progress>

				<vui-progress>
					<vui-progress-bar width="75"></vui-progress-bar>
				</vui-progress>

				<vui-progress>
					<vui-progress-bar width="100"></vui-progress-bar>
				</vui-progress>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="labels">Labels</h3>
		<p>
			Add labels to your progress bars by adding the <code>label</code> prop and
			setting it's value to the text you want displayed.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-progress>
					<vui-progress-bar width="25" label="25%"></vui-progress-bar>
				</vui-progress>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="width">Width & Height</h3>
		<p>
			To adjust the height of a progress bar component, you add the <code>height</code> prop 
			to the <code>vui-progress</code>. Height values can be set as <code>px, em, rem or %</code>.
			This will will automatically adjust the height of the <code>vui-progress-bar</code> accordingly.
		</p>
		<p>
			The progress of the the <code>vui-progress-bar</code> is set by adjusting the value
			of the <code>width</code> prop. Values are internally set to percentages, so you only 
			need to give it a <code>number</code> without the <code>%</code> symbol.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-progress height="2px" class="mb-3">
					<vui-progress-bar width="25"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="10px" class="mb-3">
					<vui-progress-bar width="25"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="20px" class="mb-3">
					<vui-progress-bar width="25"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="30px">
					<vui-progress-bar width="25"></vui-progress-bar>
				</vui-progress>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="colors">Colors</h3>
		<p>
			By default all <code>vui-progress-bar</code>'s have a <code>primary</code> background 
			color. Add the <code>color</code> prop to change the color to any of the following:
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-progress height="30px" class="mb-3">
					<vui-progress-bar width="25" label="Primary (Default)"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="30px" class="mb-3">
					<vui-progress-bar width="35" color="success" label="Success"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="30px" class="mb-3">
					<vui-progress-bar width="45" color="secondary" label="Secondary"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="30px" class="mb-3">
					<vui-progress-bar width="55" color="info" label="Info"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="30px" class="mb-3">
					<vui-progress-bar width="65" color="warning" label="Warning"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="30px" class="mb-3">
					<vui-progress-bar width="85" color="dark" label="Dark"></vui-progress-bar>
				</vui-progress>

				<vui-progress height="30px" class="mb-3">
					<vui-progress-bar width="100" color="danger" label="Danger"></vui-progress-bar>
				</vui-progress>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="multiple">Multiple Bars</h3>
		<p>
			Include multiple progress bars in a progress component to create custom horizontally stacked 
			progress bars.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-progress height="30px">
					<vui-progress-bar width="55" label="55%" color="success"></vui-progress-bar>
					<vui-progress-bar width="25" label="15%" color="warning"></vui-progress-bar>
					<vui-progress-bar width="10" label="10%" color="danger"></vui-progress-bar>
				</vui-progress>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="striped">Striped Bars</h3>
		<p>
			Add the <code>striped</code> prop to apply a striped gradient over the 
			<code>vui-progress-bar</code>'s background.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-progress class="mb-3">
					<vui-progress-bar width="25" striped></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="35" color="success" striped></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="45" color="secondary" striped></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="55" color="info" striped></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="65" color="warning" striped></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="85" color="dark" striped></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="100" color="danger" striped></vui-progress-bar>
				</vui-progress>
			</template>
			<template slot="code">{{ example6 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="animated">Animated Stripes</h3>
		<p>
			The striped gradient can also be animated. Add the <code>animated</code> prop to animate 
			the stripes right to left.
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-progress class="mb-3">
					<vui-progress-bar width="25" striped animated></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="35" color="success" striped animated></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="45" color="secondary" striped animated></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="55" color="info" striped animated></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="65" color="warning" striped animated></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="85" color="dark" striped animated></vui-progress-bar>
				</vui-progress>

				<vui-progress class="mb-3">
					<vui-progress-bar width="100" color="danger" striped animated></vui-progress-bar>
				</vui-progress>
			</template>
			<template slot="code">{{ example7 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-progress>
	<vui-progress-bar width="0"></vui-progress-bar>
</vui-progress>

<vui-progress>
	<vui-progress-bar width="25"></vui-progress-bar>
</vui-progress>

<vui-progress>
	<vui-progress-bar width="50"></vui-progress-bar>
</vui-progress>

<vui-progress>
	<vui-progress-bar width="75"></vui-progress-bar>
</vui-progress>

<vui-progress>
	<vui-progress-bar width="100"></vui-progress-bar>
</vui-progress>
`
const ex2 = `
<vui-progress>
	<vui-progress-bar width="25" label="25%"></vui-progress-bar>
</vui-progress>
`
const ex3 = `
<vui-progress height="2px" class="mb-3">
	<vui-progress-bar width="25"></vui-progress-bar>
</vui-progress>

<vui-progress height="10px" class="mb-3">
	<vui-progress-bar width="25"></vui-progress-bar>
</vui-progress>

<vui-progress height="20px" class="mb-3">
	<vui-progress-bar width="25"></vui-progress-bar>
</vui-progress>

<vui-progress height="30px">
	<vui-progress-bar width="25"></vui-progress-bar>
</vui-progress>
`
const ex4 = `
<vui-progress height="30px" class="mb-3">
	<vui-progress-bar width="25" label="Primary (Default)"></vui-progress-bar>
</vui-progress>

<vui-progress height="30px" class="mb-3">
	<vui-progress-bar width="35" color="success" label="Success"></vui-progress-bar>
</vui-progress>

<vui-progress height="30px" class="mb-3">
	<vui-progress-bar width="45" color="secondary" label="Secondary"></vui-progress-bar>
</vui-progress>

<vui-progress height="30px" class="mb-3">
	<vui-progress-bar width="55" color="info" label="Info"></vui-progress-bar>
</vui-progress>

<vui-progress height="30px" class="mb-3">
	<vui-progress-bar width="65" color="warning" label="Warning"></vui-progress-bar>
</vui-progress>

<vui-progress height="30px" class="mb-3">
	<vui-progress-bar width="85" color="dark" label="Dark"></vui-progress-bar>
</vui-progress>

<vui-progress height="30px" class="mb-3">
	<vui-progress-bar width="100" color="danger" label="Danger"></vui-progress-bar>
</vui-progress>
`
const ex5 = `
<vui-progress height="30px">
	<vui-progress-bar width="55" label="75%" color="success"></vui-progress-bar>
	<vui-progress-bar width="25" label="15%" color="waring"></vui-progress-bar>
	<vui-progress-bar width="10" label="10%" color="danger"></vui-progress-bar>
</vui-progress>
`
const ex6 = `
<vui-progress class="mb-3">
	<vui-progress-bar width="25" striped></vui-progress-bar>
</vui-progress>

<vui-progress class="mb-3">
	<vui-progress-bar width="35" color="success" striped></vui-progress-bar>
</vui-progress>

<vui-progress class="mb-3">
	<vui-progress-bar width="45" color="secondary" striped></vui-progress-bar>
</vui-progress>

<vui-progress class="mb-3">
	<vui-progress-bar width="55" color="info" striped></vui-progress-bar>
</vui-progress>

<vui-progress class="mb-3">
	<vui-progress-bar width="65" color="warning" striped></vui-progress-bar>
</vui-progress>

<vui-progress class="mb-3">
	<vui-progress-bar width="85" color="dark" striped></vui-progress-bar>
</vui-progress>

<vui-progress class="mb-3">
	<vui-progress-bar width="100" color="danger" striped></vui-progress-bar>
</vui-progress>
`
const ex7 = `
<vui-progress class="mb-3">
	<vui-progress-bar width="25" striped animated></vui-progress-bar>
</vui-progress>
`
const ex8 = `

`
const ex9 = `

`

export default {
	name: 'Docs-ProgressBars',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			example6: ex6,
			example7: ex7,
			example8: ex8,
			example9: ex9
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Overview', link: 'overview' },
			{ label: 'Labels', link: 'labels' },
			{ label: 'Width & Height', link: 'width' },
			{ label: 'Colors', link: 'colors' },
			{ label: 'Multiple Bars', link: 'multiple' },
			{ label: 'Striped', link: 'striped' },
			{ label: 'Animated Stripes', link: 'animated' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


