<template>
	<div class="container">
		<h1 class="display-4">Badges</h1>
		<p class="lead">
			Documentation and examples for badges, our small count 
			and labeling component.
		</p>

		<h3 id="example">Example</h3>
		<p>
			Badges scale to match the size of the immediate parent 
			element by using relative font sizing and em units.
		</p>

		<vui-code-panel>
			<template slot="example">
				<h1>Example heading <vui-badge>New</vui-badge></h1>
				<h2>Example heading <vui-badge>New</vui-badge></h2>
				<h3>Example heading <vui-badge>New</vui-badge></h3>
				<h4>Example heading <vui-badge>New</vui-badge></h4>
				<h5>Example heading <vui-badge>New</vui-badge></h5>
				<h6>Example heading <vui-badge>New</vui-badge></h6>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br>

		<p>Badges can be used as part of links or buttons to provide a counter.</p>
		
		<vui-code-panel>
			<template slot="example">
				<vui-button color="primary">
					Notifications <vui-badge color="light">4</vui-badge>
				</vui-button>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		
		<br><br>

		<h3 id="coloring">Colors</h3>
		<p>
			Change any of the below color prop to change the 
			appearance of a badge.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-badge color="primary">Primary</vui-badge>
				<vui-badge color="secondary">Secondary</vui-badge>
				<vui-badge color="success">Success</vui-badge>
				<vui-badge color="danger">Danger</vui-badge>
				<vui-badge color="warning">Warning</vui-badge>
				<vui-badge color="info">Info</vui-badge>
				<vui-badge color="light">Light</vui-badge>
				<vui-badge color="dark">Dark</vui-badge>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">color</th>
					<td>Sets the color of the badge</td>
					<td>primary, secondary, success, danger, warning, info, light, dark</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="pill_badges">Pill Badges</h3>
		<p>
			Use the <code>pill</code> prop to make badges more 
			rounded (with a larger border-radius and additional horizontal padding).
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-badge color="primary" pill>Primary</vui-badge>
				<vui-badge color="secondary" pill>Secondary</vui-badge>
				<vui-badge color="success" pill>Success</vui-badge>
				<vui-badge color="danger" pill>Danger</vui-badge>
				<vui-badge color="warning" pill>Warning</vui-badge>
				<vui-badge color="info" pill>Info</vui-badge>
				<vui-badge color="light" pill>Light</vui-badge>
				<vui-badge color="dark" pill>Dark</vui-badge>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">pill</th>
					<td>Changes the bage to a pill shape</td>
					<td>boolean</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<h1>Example heading <vui-badge>New</vui-badge></h1>
<h2>Example heading <vui-badge>New</vui-badge></h2>
<h3>Example heading <vui-badge>New</vui-badge></h3>
<h4>Example heading <vui-badge>New</vui-badge></h4>
<h5>Example heading <vui-badge>New</vui-badge></h5>
<h6>Example heading <vui-badge>New</vui-badge></h6>
`
const ex2 = `
<vui-button color="primary">
	Notifications <vui-badge color="light">4</vui-badge>
</vui-button>
`

const ex3 = `
<vui-badge color="primary">Primary</vui-badge>
<vui-badge color="secondary">Secondary</vui-badge>
<vui-badge color="success">Success</vui-badge>
<vui-badge color="danger">Danger</vui-badge>
<vui-badge color="warning">Warning</vui-badge>
<vui-badge color="info">Info</vui-badge>
<vui-badge color="light">Light</vui-badge>
<vui-badge color="dark">Dark</vui-badge>
`

const ex4 = `
<vui-badge color="primary" pill>Primary</vui-badge>
<vui-badge color="secondary" pill>Secondary</vui-badge>
<vui-badge color="success" pill>Success</vui-badge>
<vui-badge color="danger" pill>Danger</vui-badge>
<vui-badge color="warning" pill>Warning</vui-badge>
<vui-badge color="info" pill>Info</vui-badge>
<vui-badge color="light" pill>Light</vui-badge>
<vui-badge color="dark" pill>Dark</vui-badge>
`

export default {
	name: 'Docs-Badge',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Example', link: 'example' },
			{ label: 'Coloring', link: 'coloring' },
			{ label: 'Pill Badges', link: 'pill_badges' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


