<template>
	<div class="container">
		<h1 class="display-4">Roadmap</h1>
		<p class="lead">
			Documentation and examples for how to use Vuis included navigation components.
		</p>

		<br>

		<h3 id="road_ahead">The Road Ahead</h3>
		<p>
			My vision for Vui is to be the most complete component library available. Below I have outlined
			the next set of components and features that I am working on and that will become available
			as time and resources arise.
		</p>
		
		<br>

		<h4 id="components">Components</h4>
		<ul>
			<li>Toggle Switches</li>
			<li>Tab Panels</li>
			<li>Progress Loaders</li>
			<li>Dialouges</li>
			<li>Chrome Windows</li>
			<li>Drawer Panels</li>
			<li>Charts & Graphs</li>
			<li>Tags</li>
			<li>Type Ahead</li>
			<li>Grid System</li>
		</ul>

		<br>

		<h4 id="features">Features</h4>
		<ul>
			<li>Style Generator</li>
			<li>Emit support for VSCode & Atom</li>
			<li>Support for Fontawesome 4 & 5 icons</li>
			<li>Material Design Styles</li>
			<li>NPM Packages</li>
			<li>Custom Themes & Layouts</li>
		</ul>

		<br><br>

		<h3 id="tools">New Tools</h3>
		<p>
			I am currently in the design phase on several tools that I think would be 
			a great addition for designers and developers. If there is enough community support these 
			tools can become a reality.
		</p>
		<br>
		<h5 id="style_generator">Custom CSS Style Generator</h5>
		<p>
			This would be an online tool that allows you to quickly create your own styles
			and colors for customizing any of the components.
		</p>

		<br>
		<h5 id="component_explorer">Component Explorer</h5>
		<p>
			The component explorer is a live sandbox that lets you play with any
			of the components. It shows you the components code and you can manipulate it and
			play with it to get a better understanding of how they work.
		</p>

		<br>
		<h5 id="vui_studio">Vui Studio</h5>
		<p>
			Vui Studio is a powerful desktop app for designing and prototyping Vue applications. It supports all of Vui's components 
			which you can drag and drop to assemble application templates. It is built on top of the Vue framework, 
			and exports clean and semantic HTML.  
		</p>


		<br><br><br><br>
	</div>
</template>

<script>
export default {
	name: 'Docs-Roadmap',
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'The Road Ahead', link: 'road_ahead' },
			{ label: '- Components', link: 'components' },
			{ label: '- Features', link: 'features' },
			{ label: 'New Tools', link: 'tools' },
			{ label: '- Style Generator', link: 'style_generator' },
			{ label: '- Component Explorer', link: 'component_explorer' },
			{ label: '- Vui Studio', link: 'vui_studio' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


