<template>
	<div class="container">
		<h1 class="display-4">Alerts</h1>
		<p class="lead">
			Provide contextual feedback messages for typical 
			user actions with the handful of available and 
			flexible alert messages.
		</p>

		<h3 id="examples">Examples</h3>
		<p>
			Alerts are available for any length of text, as well 
			as an optional dismiss button. For proper styling, 
			use one of the eight required contextual classes 
			(e.g., color="success").
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-alert color="primary">
					A simple primary alertcheck it out!
				</vui-alert>
				<vui-alert color="secondary">
					A simple secondary alertcheck it out!
				</vui-alert>
				<vui-alert color="success">
					A simple success alertcheck it out!
				</vui-alert>
				<vui-alert color="danger">
					A simple danger alertcheck it out!
				</vui-alert>
				<vui-alert color="warning">
					A simple warning alertcheck it out!
				</vui-alert>
				<vui-alert color="info">
					A simple info alertcheck it out!
				</vui-alert>
				<vui-alert color="light">
					A simple light alertcheck it out!
				</vui-alert>
				<vui-alert color="dark">
					A simple dark alertcheck it out!
				</vui-alert>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="link_color">Link Color</h3>
		<p>
			Use the <code>&lt;vui-alert-link&gt;</code> prop to quickly provide 
			matching colored links within any alert.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-alert color="primary">
					 A simple primary alert with
					 <vui-alert-link href="#">an example link</vui-alert-link>.
				</vui-alert>
				<vui-alert color="secondary">
					A simple secondary alert with 
					<vui-alert-link href="#">an example link</vui-alert-link>. 
				</vui-alert>
				<vui-alert color="success">
					A simple success alert with 
					<vui-alert-link href="#">an example link</vui-alert-link>.
				</vui-alert>
				<vui-alert color="danger">
					A simple danger alert with 
					<vui-alert-link href="#">an example link</vui-alert-link>.
				</vui-alert>
				<vui-alert color="warning">
					A simple warning alert with 
					<vui-alert-link href="#">an example link</vui-alert-link>.
				</vui-alert>
				<vui-alert color="info">
					A simple info alert with 
					<vui-alert-link href="#">an example link</vui-alert-link>.
				</vui-alert>
				<vui-alert color="light">
					A simple light alert with 
					<vui-alert-link href="#">an example link</vui-alert-link>.
				</vui-alert>
				<vui-alert color="dark">
					A simple dark alert with 
					<vui-alert-link href="#">an example link</vui-alert-link>. 
				</vui-alert>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="additional_content">Additional Content</h3>
		<p>
			Alerts can also contain additional HTML elements like 
			headings, paragraphs and dividers.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-alert color="success">
					 <h4>Well done!</h4>
  				<p>
						Aww yeah, you successfully read this important alert message. 
						This example text is going to run a bit longer so that you 
						can see how spacing within an alert works with this kind of content.
					</p>
  				<hr>
  				<p class="mb-0">
						Whenever you need to, be sure to use margin utilities to 
						keep things nice and tidy.
					</p>
				</vui-alert>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="dismissing">Dismissing</h3>
		<p>
			Using the <code>dismissible</code> prop, its possible to dismiss 
			any alert inline. You can see this in action with a live demo:
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-alert color="warning" dismissible>
					<strong>Holy guacamole!</strong> You should check in on some 
					of those fields below.
				</vui-alert>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="timed_dismissal">Timed Dismissal</h3>
		<p>
			Using the <code>timer</code> prop, its possible to dismiss 
			any alert after a set amount of time. You can see this in 
			action with a live demo:
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-input-group>
					<vui-input 
						placeholder="Enter time in miliseconds. e.g. 3 seconds = 3000" 
						ref="time"
					/>
					<vui-input-group-append>
						<vui-button @click="setTime">Set Timer</vui-button>
					</vui-input-group-append>
				</vui-input-group>

				<template v-if="showTimer">
					<vui-alert color="warning" :timer="parseInt(isTimer)">
						<strong>Holy guacamole!</strong> I will disapear in {{ isTimer }} miliseconds.
					</vui-alert>
				</template>
				<template v-else>
					<vui-alert color="warning">
						<strong>Holy guacamole!</strong> You should check in on some 
						of those fields below.
					</vui-alert>
				</template>
				
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-alert color="primary">
	A simple primary alertcheck it out!
</vui-alert>
<vui-alert color="secondary">
	A simple secondary alertcheck it out!
</vui-alert>
<vui-alert color="success">
	A simple success alertcheck it out!
</vui-alert>
<vui-alert color="danger">
	A simple danger alertcheck it out!
</vui-alert>
<vui-alert color="warning">
	A simple warning alertcheck it out!
</vui-alert>
<vui-alert color="info">
	A simple info alertcheck it out!
</vui-alert>
<vui-alert color="light">
	A simple light alertcheck it out!
</vui-alert>
<vui-alert color="dark">
	A simple dark alertcheck it out!
</vui-alert>
`
const ex2 = `
<vui-alert color="primary">
	A simple primary alert with
	<vui-alert-link href="#">an example link</vui-alert-link>.
</vui-alert>
<vui-alert color="secondary">
	A simple secondary alert with 
	<vui-alert-link href="#">an example link</vui-alert-link>. 
</vui-alert>
<vui-alert color="success">
	A simple success alert with 
	<vui-alert-link href="#">an example link</vui-alert-link>.
</vui-alert>
<vui-alert color="danger">
	A simple danger alert with 
	<vui-alert-link href="#">an example link</vui-alert-link>.
</vui-alert>
<vui-alert color="warning">
	A simple warning alert with 
	<vui-alert-link href="#">an example link</vui-alert-link>.
</vui-alert>
<vui-alert color="info">
	A simple info alert with 
	<vui-alert-link href="#">an example link</vui-alert-link>.
</vui-alert>
<vui-alert color="light">
	A simple light alert with 
	<vui-alert-link href="#">an example link</vui-alert-link>.
</vui-alert>
<vui-alert color="dark">
	A simple dark alert with 
	<vui-alert-link href="#">an example link</vui-alert-link>. 
</vui-alert>
`

const ex3 = `
<vui-alert color="success">
	<h4>Well done!</h4>
	<p>
		Aww yeah, you successfully read this important alert message. 
		This example text is going to run a bit longer so that you 
		can see how spacing within an alert works with this kind of content.
	</p>
	<hr>
	<p class="mb-0">
		Whenever you need to, be sure to use margin utilities to 
		keep things nice and tidy.
	</p>
</vui-alert>
`

const ex4 = `
<vui-alert color="warning" dismissible>
	<strong>Holy guacamole!</strong> You should check in on some 
	of those fields below.
</vui-alert>
`
const ex5 = `
<vui-alert color="warning" timer="3000">
	<strong>Holy guacamole!</strong> You should check in on some 
	of those fields below.
</vui-alert>
`

export default {
	name: 'Docs-Alert',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5,
			timer: null,
			showTimer: false
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Examples', link: 'examples' },
			{ label: 'Link Color', link: 'link_color' },
			{ label: 'Additional Content', link: 'additional_content' },
			{ label: 'Dismissing', link: 'dismissing' },
			{ label: 'Timed Dismissal', link: 'timed_dismissal' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	computed: {
		isTimer() {
			return this.timer !== null ? this.timer : null
		}
	},
	methods: {
		setTime() {
			this.showTimer = true
			this.timer = this.$refs.time.$el.value
			this.$refs.time.$el.value = 'Refresh the page to reset.'
		},
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


