<template>
	<div class="container">
		<h1 class="display-4">Introduction</h1>
		<p class="lead">
			Get started with Vui components built with Vue 2 & Bootstrap 4. Unlike many of the other
			libraries, this project was designed to be easier to use for designers and front-end developers.
		</p>

		<vui-callout>
			Vui is intedned to be use with a Vue 2.x CLI Webpack project. It will not work with Vue 1.x or if your
			project includes Vue.js as a script file.
		</vui-callout>
		<br>

		<h3 id="about">About</h3>
		<p>
			My background is in UI/UX design and development. So my goal was to provide a library of components
			that gives you the best possible user experience in using these components and the components were
			desinged to give your users the best possible user expereince.
		</p>
		<p>
			Many other libraires write their components in pure Javascript. This can be very difficult
			if your not a highly expereinced programmer. I wrote these components in Vue using the Vue single file 
			component pattern, becasue this is how you build your applications. 
		</p>
		<p>Compare the following code:</p>
		<br>
		<h5>Vue.js Render Template Code for an Alert Component</h5>
		<vui-code-panel type="javascript">
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		<br>
		<h5>Vui Template Code for an Alert Component</h5>
		<vui-code-panel>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<br><br>
		<p>
			Maybe it's just me, but I find the Vui template code to be much easier to understand and make
			sense of. So if your like me, then Vui is for you!
		</p>
		<p>
			It was also very important to me that the code for these compoents be accesible to you. In most cases
			the componets can be used as is and will provide the exact functionality that you need. But there
			may be cases where you would like to extend or change how a component works. So I wrote the code as shown 
			above so that you can easily copy and rename a component and then modify it for your own needs, then register it as
			a new component.
		</p>

		<br><br>

		<h3 id="dependencies">Dependencies</h3>
		<p>
			Vui has no dependency on jQuery or Bootstrap's Javascript, however a few of the components
			require the following libraries. Listed below are the required dependencies:
		</p>
		<p>Vui Core:</p>
		<ul>
			<li>
				<a href="https://vuejs.org/" target="_blank">Vue 2</a> <br>
			</li>
			<li>
				<a href="https://github.com/vuejs/vue-cli" target="_blank">Vue-CLI</a>
			</li>
			<li>
				<a href="https://getbootstrap.com/" target="_blank">Bootstrap 4.1</a>
			</li>
			<li>
				<a href="https://fontawesome.com/v3.2.1/icons/" target="_blank">Font Awesome 3.2.1</a>
			</li>
		</ul>
		<p>Vui Component: CodePanel</p>
		<ul>
			<li>
				<a href="https://prismjs.com/" target="_blank">Prism Syntax Highligter</a>
			</li>
			<li>
				<a href="https://clipboardjs.com/" target="_blank">Clipboard.js</a>
			</li>
		</ul>
		<p>Vui Utility: Draggable</p>
		<ul>
			<li>
				<a href="https://greensock.com/gsap" target="_blank">Greensock Animation Platform</a>
			</li>
		</ul>

		<br><br>

		<h3 id="browser_support">Browser Support</h3>
		<p>
			Vui supports all the browsers and devices that 
			<a href="https://getbootstrap.com/docs/4.1/getting-started/browsers-devices/" target="_blank">Bootstrap 4.1 CSS</a> 
			& <a href="https://github.com/vuejs/vue" target="_blank">Vue 2</a> support.
		</p>

		<br>

		<h3 id="contribution">Contribution</h3>
		<p>
			Finding bugs and issues, have a feature request or how to imporve the docs? Any contribution is welcome and greatly
			appreciated. To get started, head over to the Code of Conduct.
		</p>

		<br>

		<h3 id="license">License</h3>
		<p>
			Vui is open source and released under the <a href="#" target="_blank">MIT License</a>.
		</p>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
render (h) {
	if (!this.localShow) {
		// If not showing, render placeholder
		return h(false)
	}
	let dismissBtn = h(false)
	if (this.dismissible) {
		// Add dismiss button
		dismissBtn = h(
			'b-button-close',
			{ attrs: { 'aria-label': this.dismissLabel }, on: { click: this.dismiss } },
			[ this.$slots.dismiss ]
		)
	}
	const alert = h(
		'div',
		{class: this.classObject, attrs: {role: 'alert', 'aria-live': 'polite', 'aria-atomic': true}},
		[dismissBtn, this.$slots.default]
	)
	return !this.fade ? alert : h(
		'transition',
		{ props: { name: 'fade', appear: true } },
		[ alert ]
	)
}
`
const ex2 = `
<template>
	<div class="alert" :class="hasColor" role="alert" v-show="isShown">
		<slot></slot>
		<vui-button @click="close" v-show="dismissible">
			<vui-icon type"remove" />
		</vui-button>
	</div>
</template>
`

export default {
	name: 'Docs-Intro',
	data() {
		return {
			example1: ex1,
			example2: ex2
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'About', link: 'about' },
			{ label: 'Dependencies', link: 'dependencies' },
			{ label: 'Browser Support', link: 'browser_support' },
			{ label: 'Contribution', link: 'contribution' },
			{ label: 'License', link: 'license' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


