<template>
	<div class="container">
		<h1 class="display-4">Colors</h1>
		<p class="lead">
			Convey meaning through color with the color utility directive. Includes support for styling links with hover states, too.
		</p>

		<br>

		<h3 id="color">Color</h3>
		<vui-code-panel>
			<template slot="example">
				<p v-color:txt="'primary'">v-color:txt="'primary'"</p>
				<p v-color:txt="'secondary'">v-color:txt="'secondary'"</p>
				<p v-color:txt="'success'">v-color:txt="'success'"</p>
				<p v-color:txt="'danger'">v-color:txt="'danger'"</p>
				<p v-color:txt="'warning'">v-color:txt="'warning'"</p>
				<p v-color:txt="'info'">v-color:txt="'info'"</p>
				<p v-color:txt="'light'" v-color:bg="'dark'">v-color:txt="'light'"</p>
				<p v-color:txt="'dark'">v-color:txt="'dark'"</p>
				<p v-color:txt="'body'">v-color:txt="'body'"</p>
				<p v-color:txt="'muted'">v-color:txt="'muted'"</p>
				<p v-color:txt="'white'" v-color:bg="'dark'">v-color:txt="'white'"</p>
				<p v-color:txt="'black-50'">v-color:txt="'black-50'"</p>
				<p v-color:txt="'white-50'" v-color:bg="'dark'">v-color:txt="'white-50'"</p>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		<br>
		<p>
			Contextual text also works well on anchors and router-links with the provided hover and focus states. 
			Note that the <code>white</code> and <code>muted</code> has no link styling.
		</p>
		<vui-code-panel>
			<template slot="example">
				<p><a href="#" v-color:txt="'primary'">Primary link</a></p>
				<p><a href="#" v-color:txt="'secondary'">Secondary link</a></p>
				<p><a href="#" v-color:txt="'success'">Success link</a></p>
				<p><a href="#" v-color:txt="'danger'">Danger link</a></p>
				<p><a href="#" v-color:txt="'warning'">Warning link</a></p>
				<p><a href="#" v-color:txt="'info'">Info link</a></p>
				<p><a href="#" v-color:txt="'light'" v-color:bg="'dark'">Light link</a></p>
				<p><a href="#" v-color:txt="'dark'">Dark link</a></p>
				<p><a href="#" v-color:txt="'muted'">Muted link</a></p>
				<p><a href="#" v-color:txt="'white'" v-color:bg="'dark'">White link</a></p>

				<hr>

				<p><router-link to="/docs/utilities/colors" v-color:txt="'primary'">Primary router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'secondary'">Secondary router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'success'">Success router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'danger'">Danger router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'warning'">Warning router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'info'">Info router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'light'" v-color:bg="'dark'">Light router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'dark'">Dark router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'muted'">Muted router-link</router-link></p>
				<p><router-link to="/docs/utilities/colors" v-color:txt="'white'" v-color:bg="'dark'">White router-link</router-link></p>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>

		<br><br>

		<h3 id="background_color">Background Color</h3>
		<p>
			Similar to the contextual text color, easily set the background of an element to any contextual color. 
			Anchor components will darken on hover, just like with text. Background utilities do not set text color, 
			so in some cases youll want to use <code>v-color:txt</code> utilities.
		</p>
		<vui-code-panel>
			<template slot="example">
				<p class="p-3" v-color:bg="'primary'" v-color:txt="'white'">v-color:bg="'primary'"</p>
				<p class="p-3" v-color:bg="'secondary'" v-color:txt="'white'">v-color:bg="'secondary'"</p>
				<p class="p-3" v-color:bg="'success'" v-color:txt="'white'">v-color:bg="'success'"</p>
				<p class="p-3" v-color:bg="'danger'" v-color:txt="'white'">v-color:bg="'danger'"</p>
				<p class="p-3" v-color:bg="'warning'" v-color:txt="'white'">v-color:bg="'warning'"</p>
				<p class="p-3" v-color:bg="'info'" v-color:txt="'white'">v-color:bg="'info'"</p>
				<p class="p-3" v-color:bg="'light'"  v-color:txt="'dark'">v-color:bg="'light'"</p>
				<p class="p-3" v-color:bg="'dark'" v-color:txt="'white'">v-color:bg="'dark'"</p>
				<p class="p-3" v-color:bg="'white'"  v-color:txt="'dark'">v-color:bg="'white'"</p>
				<p class="p-3" v-color:bg="'transparent'"  v-color:txt="'dark'">v-color:bg="'transparent'"</p>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>


		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<p v-color:txt="'primary'">v-color:txt="'primary'"</p>
<p v-color:txt="'secondary'">v-color:txt="'secondary'"</p>
<p v-color:txt="'success'">v-color:txt="'success'"</p>
<p v-color:txt="'danger'">v-color:txt="'danger'"</p>
<p v-color:txt="'warning'">v-color:txt="'warning'"</p>
<p v-color:txt="'info'">v-color:txt="'info'"</p>
<p v-color:txt="'light'">v-color:txt="'light'"</p>
<p v-color:txt="'dark'">v-color:txt="'dark'"</p>
<p v-color:txt="'body'">v-color:txt="'body'"</p>
<p v-color:txt="'muted'">v-color:txt="'muted'"</p>
<p v-color:txt="'white'">v-color:txt="'white'"</p>
<p v-color:txt="'black-50'">v-color:txt="'black-50'"</p>
<p v-color:txt="'white-50'">v-color:txt="'white-50'"</p>
`
const ex2 = `
<a href="#" v-color:txt="'primary'">Primary link</a>

<router-link to="/" v-color:txt="'primary'">Primary router-link</router-link>
`
const ex3 = `
<p v-color:bg="'primary'" v-color:txt="'white'">v-color:bg="'primary'"</p>
<p v-color:bg="'secondary'" v-color:txt="'white'">v-color:bg="'secondary'"</p>
<p v-color:bg="'success'" v-color:txt="'white'">v-color:bg="'success'"</p>
<p v-color:bg="'danger'" v-color:txt="'white'">v-color:bg="'danger'"</p>
<p v-color:bg="'warning'" v-color:txt="'white'">v-color:bg="'warning'"</p>
<p v-color:bg="'info'" v-color:txt="'white'">v-color:bg="'info'"</p>
<p v-color:bg="'light'"  v-color:txt="'dark'">v-color:bg="'light'"</p>
<p v-color:bg="'dark'" v-color:txt="'white'">v-color:bg="'dark'"</p>
<p v-color:bg="'white'"  v-color:txt="'dark'">v-color:bg="'white'"</p>
<p v-color:bg="'transparent'"  v-color:txt="'dark'">v-color:bg="'transparent'"</p>
`

export default {
	name: 'Docs-Colors',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Color', link: 'color' },
			{ label: 'Background Color', link: 'background_color' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


