<template>
	<div class="container">
		<h1 class="display-4">Image Placeholder</h1>
		<p class="lead">
			Documentation and examples for image placeholders, our easy to
			use image generator for prototyping. 
		</p>

		<br><br>
		<p>
			A lightweight, flexible component that can ease adding temp images
			to your application. 
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-img-placeholder size="286x180" color="secondary" text="Profile Image" />
				<vui-img-placeholder size="320x260" color="primary" class="ml-2" />
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">size</th>
					<td>Sets the size of the image</td>
					<td>values must follow this format [width]x[height] i.e. 286x180</td>
				</tr>
				<tr>
					<th scope="row">color</th>
					<td>Sets the background color of the image</td>
					<td>primary, secondary, success, danger, warning, info, dark</td>
				</tr>
				<tr>
					<th scope="row">text</th>
					<td>Sets the text to display on the image</td>
					<td>
						A-z (upper and lowercase), numbers and most symbols will work. <br>
						If you do not provide text, the image size will be displayed.
					</td>
				</tr>
			</tbody>
		</table>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-img-placeholder size="286x180" color="secondary" text="Profile Image" />
<vui-img-placeholder size="320x260" color="primary" class="ml-2" />
`

export default {
	name: 'Docs-ImgPlaceholder',
	data() {
		return {
			example1: ex1
		}
	},
	mounted() {
		this.$parent.tocLinks = []

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500);
		}
	}
}
</script>


