<template>
	<div class="container">
		<h1 class="display-4">Accordion</h1>
		<p class="lead">
			Vui accordion is a component which organizes content within 
			collapsable items. Accordion allows showing only one collapsed 
			item at the same time.
		</p>
		<p>
			Accordions can toggle through a number of text blocks with a 
			single click, and that greatly increases the UX of your project.
		</p>

		<h3 id="examples">Examples</h3>
		<p>See the following Vui Accordion example:</p>

		<vui-code-panel>
			<template slot="example">
				<vui-accordion>
					<vui-accordion-panel header="Panel 1">
						This is accodion panel 1 content. 
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 2">
						This is accodion panel 2 content.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 3">
						This is accodion panel 3 content.
					</vui-accordion-panel>
				</vui-accordion>
			</template>
			<template slot="code">{{ example1 }}</template>
		</vui-code-panel>
		
		<br><br>

		<h3 id="header_title">Header Title</h3>
		<p>
			To add a title to the panel, simply add the <code>header</code> prop 
			and give it a value:
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-accordion>
					<vui-accordion-panel header="Panel 1">
						This is accodion panel 1 content.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 2">
						This is accodion panel 2 content.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 3">
						This is accodion panel 3 content.
					</vui-accordion-panel>
				</vui-accordion>
			</template>
			<template slot="code">{{ example2 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">header</th>
					<td>Sets the text for the header.</td>
					<td>Plain text</td>
				</tr>
			</tbody>
		</table>

		<br><br>
		
		<h3 id="initial_visibility">Initial visibility (start expanded)</h3>
		<p>
			To make the <code>&lt;vui-accordion-panel&gt;</code> show initially, set the 
			<code>active</code> prop.
			Which ever panell you set active will initially be shown.
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-accordion>
					<vui-accordion-panel header="Panel 1" active>
						This is accodion panel 1 content.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 2">
						This is accodion panel 2 content.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 3">
						This is accodion panel 3 content.
					</vui-accordion-panel>
				</vui-accordion>
			</template>
			<template slot="code">{{ example3 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">active</th>
					<td>Expands the panel.</td>
					<td>boolean</td>
				</tr>
			</tbody>
		</table>

		<br><br>

		<h3 id="active_colors">Active Colors</h3>
		<p>
			You can set colors for the <code>panel header</code> when its active.
			To set a background color simply set the <code>bgColor</code> prop and
			set its value to any of the standard Bootstrap colors:
		</p>

		<vui-code-panel>
			<template slot="example">
				<vui-accordion>
					<vui-accordion-panel header="Panel 1" bgColor="primary" active>
						This is accodion panel 1 content.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 2" bgColor="primary">
						This is accodion panel 2 content.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 3" bgColor="primary">
						This is accodion panel 3 content.
					</vui-accordion-panel>
				</vui-accordion>
			</template>
			<template slot="code">{{ example4 }}</template>
		</vui-code-panel>
		<table class="table table-bordered">
			<thead>
				<tr>
					<th scope="col">Prop</th>
					<th scope="col">Desctiption</th>
					<th scope="col">Values</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<th scope="row">bgColor</th>
					<td>Background color of the header.</td>
					<td>primary, secondary, success, danger, warning, info, dark</td>
				</tr>
			</tbody>
		</table>
		<br>
		<p>
			You can alos assign diffrent <code>bgColor</code> for individual headers:
		</p>
		<vui-code-panel>
			<template slot="example">
				<vui-accordion>
					<vui-accordion-panel header="Panel 1" bgColor="primary" active>
						This is accodion panel has a <code>Primary</code> header.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 2" bgColor="info">
						This is accodion panel has a <code>Info</code> header.
					</vui-accordion-panel>
					<vui-accordion-panel header="Panel 3" bgColor="warning">
						This is accodion panel has a <code>Warning</code> header.
					</vui-accordion-panel>
				</vui-accordion>
			</template>
			<template slot="code">{{ example5 }}</template>
		</vui-code-panel>

		<br><br><br><br>
	</div>
</template>

<script>
const ex1 = `
<vui-accordion>
	<vui-accordion-panel header="Panel 1">
		This is accodion panel 1 content.
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 2">
		This is accodion panel 2 content.
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 3">
		This is accodion panel 3 content.
	</vui-accordion-panel>
</vui-accordion>
`

const ex2 = `
<vui-accordion>
	<vui-accordion-panel header="Panel 1">
		This is accodion panel 1 content.
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 2">
		This is accodion panel 2 content.
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 3">
		This is accodion panel 3 content.
	</vui-accordion-panel>
</vui-accordion>
`

const ex3 = `
<vui-accordion-panel header="Panel 1" active>
	This is accodion panel 1 content.
</vui-accordion-panel>
`

const ex4 = `
<vui-accordion>
	<vui-accordion-panel header="Panel 1" bgColor="primary">
		This is accodion panel 1 content. 
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 2" bgColor="primary">
		This is accodion panel 2 content.
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 3" bgColor="primary">
		This is accodion panel 3 content.
	</vui-accordion-panel>
</vui-accordion>
`

const ex5 = `
<vui-accordion>
	<vui-accordion-panel header="Panel 1" bgColor="primary" active>
		This is accodion panel has a <code>Primary</code> header.
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 2" bgColor="info">
		This is accodion panel has a <code>Info</code> header.
	</vui-accordion-panel>
	<vui-accordion-panel header="Panel 3" bgColor="danger">
		This is accodion panel has a <code>Danger</code> header.
	</vui-accordion-panel>
</vui-accordion>
`

export default {
	name: 'Docs-Accordion',
	data() {
		return {
			example1: ex1,
			example2: ex2,
			example3: ex3,
			example4: ex4,
			example5: ex5
		}
	},
	mounted() {
		this.$parent.tocLinks = [
			{ label: 'Examples', link: 'examples' },
			{ label: 'Header Title', link: 'header_title' },
			{ label: 'Initial Visibility', link: 'initial_visibility' },
			{ label: 'Active Colors', link: 'active_colors' }
		]

		$("#page-content-wrapper").animate({
			scrollTop: $("#page-content-wrapper").scrollTop(0)
		}, 100)
	},
	methods: {
		scrollTo(refName) {
			let toPosition = $("#" + refName).offset().top-80
			$("#page-content-wrapper").animate({
				scrollTop: $("#page-content-wrapper").scrollTop() + toPosition
			}, 500)
		}
	}
}
</script>


