import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

import Documentation from '@/components/docs/Documentation'
import Intro from '@/components/docs/Intro'
import Download from '@/components/docs/Download'
import Roadmap from '@/components/docs/Roadmap'

import Accordion from '@/components/docs/components/Accordion'
import Alerts from '@/components/docs/components/Alerts'
import Badge from '@/components/docs/components/Badge'
import Breadcrumb from '@/components/docs/components/Breadcrumb'
import Buttons from '@/components/docs/components/Buttons'
import ButtonGroup from '@/components/docs/components/ButtonGroup'
import Callout from '@/components/docs/components/Callout'
import Card from '@/components/docs/components/Card'
import CodePanel from '@/components/docs/components/CodePanel'
import Dtable from '@/components/docs/components/Dtable'
import Dropdowns from '@/components/docs/components/Dropdowns'
import Forms from '@/components/docs/components/Forms'
import Icon from '@/components/docs/components/Icon'
import ImgPlaceholder from '@/components/docs/components/ImgPlaceholder'
import InputGroup from '@/components/docs/components/InputGroup'
import Jumbotron from '@/components/docs/components/Jumbotron'
import ListGroup from '@/components/docs/components/ListGroup'
import Modal from '@/components/docs/components/Modal'
import Navs from '@/components/docs/components/Navs'
import Navbar from '@/components/docs/components/Navbar'
import Pagination from '@/components/docs/components/Pagination'
import ProgressBars from '@/components/docs/components/ProgressBars'
import Tooltips from '@/components/docs/components/Tooltips'

import Borders from '@/components/docs/utilities/Borders'
import Draggable from '@/components/docs/utilities/Draggable'
import Colors from '@/components/docs/utilities/Colors'
import Float from '@/components/docs/utilities/Float'
import Shadows from '@/components/docs/utilities/Shadows'
import Text from '@/components/docs/utilities/Text'
import VerticalAlign from '@/components/docs/utilities/VerticalAlign'

import Patron from '@/components/Patron'

Vue.use(Router)

const routes = [
	{ path: '/', component: Home },
	{ path: '/docs', redirect: '/docs/getting-started/intro' },
	{ path: '/docs/components', redirect: '/docs/components/accordion' },
	{ path: '/docs/utilities', redirect: '/docs/utilities/borders' },

	{
		path: '/docs/:category/:topic',
		component: Documentation,
		children: [
			// Getting Started
			{ path: '/docs/getting-started/intro', component: Intro },
			{ path: '/docs/getting-started/download', component: Download },
			{ path: '/docs/getting-started/roadmap', component: Roadmap },

			// Components
			{ path: '/docs/components/accordion', component: Accordion },
			{ path: '/docs/components/alerts', component: Alerts },
			{ path: '/docs/components/badge', component: Badge },
			{ path: '/docs/components/breadcrumb', component: Breadcrumb },
			{ path: '/docs/components/buttons', component: Buttons },
			{ path: '/docs/components/button_group', component: ButtonGroup },
			{ path: '/docs/components/data_table', component: Dtable },
			{ path: '/docs/components/callout', component: Callout },
			{ path: '/docs/components/card', component: Card },
			{ path: '/docs/components/code_panel', component: CodePanel },
			{ path: '/docs/components/dropdowns', component: Dropdowns },
			{ path: '/docs/components/forms', component: Forms },
			{ path: '/docs/components/icon', component: Icon },
			{ path: '/docs/components/image_placeholder', component: ImgPlaceholder },
			{ path: '/docs/components/input_group', component: InputGroup },
			{ path: '/docs/components/jumbotron', component: Jumbotron },
			{ path: '/docs/components/list_group', component: ListGroup },
			{ path: '/docs/components/modal', component: Modal },
			{ path: '/docs/components/navs', component: Navs },
			{ path: '/docs/components/navbar', component: Navbar },
			{ path: '/docs/components/pagination', component: Pagination },
			{ path: '/docs/components/progress_bars', component: ProgressBars },
			{ path: '/docs/components/tooltips', component: Tooltips },

			// Utilities
			{ path: '/docs/utilities/borders', component: Borders },
			{ path: '/docs/utilities/draggable', component: Draggable },
			{ path: '/docs/utilities/colors', component: Colors },
			{ path: '/docs/utilities/float', component: Float },
			{ path: '/docs/utilities/shadows', component: Shadows },
			{ path: '/docs/utilities/text', component: Text },
			{ path: '/docs/utilities/vertical_align', component: VerticalAlign },
		]
	},

	{ path: '/patrons', component: Patron },
	{ path: '*', redirect: '/' }
]

export default new Router({
	mode: 'history',
	routes,
	linkActiveClass: "active"
})



